import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AdminService } from '../../services/admin/admin.service';
import { AccessService } from '../../services/access/access.service';
import { getSumByKey, getSumByKeyWithFilter, getSumByKeyWithFilters } from '../../helpers/common';
import { AuthenticateService } from '../../services/authentication/authenticate.service';

@Component({
  selector: 'ngx-branch-stock-availability',
  templateUrl: './branch-stock-availability.component.html',
  styleUrls: ['./branch-stock-availability.component.scss']
})
export class BranchStockAvailabilityComponent implements OnInit {

  searchForm!: FormGroup;
  userAccess = {view: true, edit: false, delete: false};
  currentUserData: any;
  list: any;
  currentDate: any;
  pastDate: any;
  total3kgUsedCount: any = 0;
  total5kgUsedCount: any = 0;
  total10kgUsedCount: any = 0;
  total15kgUsedCount: any = 0;
  totalTapesUsedCount: any = 0;
  totalCoversPacketUsedCount: any = 0;
  totalBillBooksUsedCount: any = 0;
  total3kgApprovedCount: any = 0;
  total5kgApprovedCount: any = 0;
  total10kgApprovedCount: any = 0;
  total15kgApprovedCount: any = 0;
  totalTapesApprovedCount: any = 0;
  totalCoversPacketApprovedCount: any = 0;
  totalBillBooksApprovedCount: any = 0;
  stockRequestsList: any;
  isSuperAdmin: Boolean = true;
  branchID: any;
  shipmentsList: any;
  branchesList: any;
  usedStockList: any;

  constructor(
    private formBuilder: FormBuilder,
    private adminService: AdminService,
    private accessService: AccessService,
    private authenticateService: AuthenticateService,
  ) { }


  ngOnInit(): void {
    const accessData = this.accessService.getActionAccess('branch-stock-availability');
    if(accessData && accessData.length > 0){
      this.userAccess.edit = accessData[0].edit;
      this.userAccess.delete = accessData[0].delete;
    }

    this.currentUserData = this.authenticateService.currentUserValue;
    if(this.currentUserData && this.currentUserData.role_type && this.currentUserData.role_type !== 'Super-Admin'){
      this.isSuperAdmin = false;
      this.branchID = this.currentUserData.branch_id;
      this.getStockAvailability();
    }

    this.currentDate = new Date().toISOString().split('T')[0];
    let now = new Date();
    const backDate = new Date(now.setDate(now.getDate() - 30));
    this.pastDate = backDate.toISOString().split('T')[0];
    
    this.searchForm = this.formBuilder.group({
      from_date: [this.pastDate, Validators.required],
      to_date: [this.currentDate],
      branch: []
    });

    this.getBranchesList();
  }

  handleDateChange = (event: any) => {
    this.searchForm.controls['to_date'].setValue(null);
    let fromDate = this.searchForm.value.from_date;
    if(fromDate){
      let now = new Date(fromDate);
      const futureDate = new Date(now.setDate(now.getDate()));
      let date: any = futureDate.toISOString().split('T');
      document.getElementById('to_date').setAttribute('min', date[0]);
    }
  }

  getBranchesList = async() => {
    try {
      let resp = await this.adminService.getData('getBranches');          
      if(resp && resp.responseCode === 1){
        this.branchesList = resp.branchesList;
      }
    } catch (error) {
      this.branchesList = {};
    }    
  }

  getStockAvailability = async() => {
    this.getShipmentsByBranch();
    this.getStockRequests();
    this.getUsedStockList();
  }

  getShipmentsByBranch = async() => {
    try {
      let branchID  = this.branchID = this.searchForm && this.searchForm.value && this.searchForm.value.branch ? this.searchForm.value.branch : this.branchID;
      if(branchID){
        let reqData = {
          branch_id: branchID,
          // from_date: this.searchForm.value.from_date ? this.searchForm.value.from_date : null,
          // to_date: this.searchForm.value.to_date ? this.searchForm.value.to_date : null,
        };
        let resp = await this.adminService.getData('getShipmentsByBranch', reqData);          
        if(resp && resp.responseCode === 1){
          this.shipmentsList = resp.shipments;
          this.total3kgUsedCount = await getSumByKey(this.shipmentsList, 'boxes_3kg');
          this.total5kgUsedCount = await getSumByKey(this.shipmentsList, 'boxes_5kg');
          this.total10kgUsedCount = await getSumByKey(this.shipmentsList, 'boxes_10kg');
          this.total15kgUsedCount = await getSumByKey(this.shipmentsList, 'boxes_15kg');
        }
      }
    } catch (error) {
      this.shipmentsList = [];
    }    
  }
  getStockRequests = async() => {
    try {
      let branchID  = this.branchID = this.searchForm && this.searchForm.value && this.searchForm.value.branch ? this.searchForm.value.branch : this.branchID;
      if(branchID){
        let reqData = {
          branch_id: branchID,
          // from_date: this.searchForm.value.from_date ? this.searchForm.value.from_date : null,
          // to_date: this.searchForm.value.to_date ? this.searchForm.value.to_date : null,
        };        
        let resp = await this.adminService.getData('getStockRequests', reqData);          
        if(resp && resp.responseCode === 1){
          this.stockRequestsList = resp.list;
          this.total3kgApprovedCount = await getSumByKeyWithFilters(this.stockRequestsList, 'approved_quantity', 'box_weight', '3KGS', 'status' , 'APPROVED');
          this.total5kgApprovedCount = await getSumByKeyWithFilters(this.stockRequestsList, 'approved_quantity', 'box_weight', '5KGS', 'status' , 'APPROVED');
          this.total10kgApprovedCount = await getSumByKeyWithFilters(this.stockRequestsList, 'approved_quantity', 'box_weight', '10KGS', 'status' , 'APPROVED');
          this.total15kgApprovedCount = await getSumByKeyWithFilters(this.stockRequestsList, 'approved_quantity', 'box_weight', '15KGS', 'status' , 'APPROVED');
          this.totalTapesApprovedCount = await getSumByKeyWithFilters(this.stockRequestsList, 'approved_quantity', 'stock_type', 'TAPES', 'status' , 'APPROVED');
          this.totalCoversPacketApprovedCount = await getSumByKeyWithFilters(this.stockRequestsList, 'approved_quantity', 'stock_type', 'COVERS PACKET', 'status' , 'APPROVED');
          this.totalBillBooksApprovedCount = await getSumByKeyWithFilters(this.stockRequestsList, 'approved_quantity', 'stock_type', 'BILL BOOKS', 'status' , 'APPROVED');
        }
      }
    } catch (error) {
      this.stockRequestsList = [];
    }    
  }

  getUsedStockList = async() => {
    try {
      let branchID  = this.branchID = this.searchForm && this.searchForm.value && this.searchForm.value.branch ? this.searchForm.value.branch : this.branchID;
      if(branchID){
        let reqData = {
          branch_id: branchID,
          // from_date: this.searchForm.value.from_date ? this.searchForm.value.from_date : null,
          // to_date: this.searchForm.value.to_date ? this.searchForm.value.to_date : null,
        };
        
        let resp = await this.adminService.getData('getUsedStockList', reqData);          
        if(resp && resp.responseCode === 1){
          this.usedStockList = resp.list;
          this.totalTapesUsedCount = await getSumByKeyWithFilter(this.usedStockList, 'quantity', 'stock_type', 'TAPES');
          this.totalCoversPacketUsedCount = await getSumByKeyWithFilter(this.usedStockList, 'quantity', 'stock_type', 'COVERS PACKET');
          this.totalBillBooksUsedCount = await getSumByKeyWithFilter(this.usedStockList, 'quantity', 'stock_type', 'BILL BOOKS');
        }
      }
    } catch (error) {

    }    
  }

}
