import { Component, OnInit, TemplateRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NbDialogService } from '@nebular/theme';
import { AdminService } from '../../services/admin/admin.service';
import { AccessService } from '../../services/access/access.service';
import { floatRegx, alnumSpecialRegx, nonHTMLRegx } from '../../helpers/regExp';
import { deleteConfirmAlert, successAlert, errorAlert } from '../../helpers/sweetalert';
import { exportToExcel } from '../../helpers/common';

@Component({
  selector: 'ngx-salaries',
  templateUrl: './salaries.component.html',
  styleUrls: ['./salaries.component.scss']
})
export class SalariesComponent implements OnInit {

  searchForm!: FormGroup;
  addForm!: FormGroup;
  updateForm!: FormGroup;
  showModal! : boolean;
  submitted = false;
  userAccess = {view: true, edit: false, delete: false};
  page: number = 1;
  pageSize: number = 25;
  totalCount: number = 0;
  pageSizes = ['25', '50', '100', '200'];
  list: any;
  currentDate: any;
  pastDate: any;

  constructor(
    private formBuilder: FormBuilder,
    private adminService: AdminService,
    private accessService: AccessService,
    private dialogService: NbDialogService,
  ) { }


  ngOnInit(): void {
    const accessData = this.accessService.getActionAccess('salaries');
    if(accessData && accessData.length > 0){
      this.userAccess.edit = accessData[0].edit;
      this.userAccess.delete = accessData[0].delete;
    }

    this.currentDate = new Date().toISOString().split('T')[0];
    let now = new Date();
    const backDate = new Date(now.setDate(now.getDate() - 30));
    this.pastDate = backDate.toISOString().split('T')[0];
    
    this.searchForm = this.formBuilder.group({
      from_date: [this.pastDate, Validators.required],
      to_date: [this.currentDate],
    });

    this.addForm = this.formBuilder.group({
      employee_name: ['', [Validators.required, Validators.pattern(alnumSpecialRegx)]],
      salary_amount: ['', [Validators.required, Validators.pattern(floatRegx)]],
      salary_date: ['', [Validators.required]],
      transaction_number: ['', [Validators.pattern(alnumSpecialRegx), Validators.maxLength(50)]],
      esi: ['', Validators.pattern(floatRegx)],
      pf: ['', Validators.pattern(floatRegx)],
      hra: ['', Validators.pattern(floatRegx)],
      description: ['', Validators.pattern(nonHTMLRegx)],
    });
    this.updateForm = this.formBuilder.group({
      id: ['', [Validators.required]],
      employee_name: ['', [Validators.required, Validators.pattern(alnumSpecialRegx)]],
      salary_amount: ['', [Validators.required, Validators.pattern(floatRegx)]],
      salary_date: ['', [Validators.required]],
      transaction_number: ['', [Validators.pattern(alnumSpecialRegx), Validators.maxLength(50)]],
      esi: ['', Validators.pattern(floatRegx)],
      pf: ['', Validators.pattern(floatRegx)],
      hra: ['', Validators.pattern(floatRegx)],
      description: ['', Validators.pattern(nonHTMLRegx)],
    });

    this.getSalaries();
  }

  changePageSize = (event: any) => {    
    this.pageSize = event.target.value;
  }

  openModal(dialog: TemplateRef<any>) {
    this.dialogService.open(
      dialog,
      {
        context: '',
        closeOnBackdropClick: false,
      });
  }

  get form() { return this.addForm.controls; }
  get editForm() { return this.updateForm.controls; }

  edit = (data: any, modalVal: any) => {
    const updateData = {
      id: data.id,
      employee_name: data.employee_name,
      salary_amount: data.salary_amount,
      salary_date: data.salary_date,
      transaction_number: data.transaction_number,
      esi: data.esi ? data.esi : '',
      pf: data.pf ? data.pf : '',
      hra: data.hra ? data.hra : '',
      description: data.description ? data.description : ''
    }
    this.updateForm.setValue(updateData);
    this.openModal(modalVal);
  }

  handleDateChange = (event: any) => {
    this.searchForm.controls['to_date'].setValue(null);
    let fromDate = this.searchForm.value.from_date;
    if(fromDate){
      let now = new Date(fromDate);
      const futureDate = new Date(now.setDate(now.getDate()));
      let date: any = futureDate.toISOString().split('T');
      document.getElementById('to_date').setAttribute('min', date[0]);
    }
  }

  getSalaries = async() => {
    try {
      let reqData = {
        from_date: this.searchForm.value.from_date ? this.searchForm.value.from_date : null,
        to_date: this.searchForm.value.to_date ? this.searchForm.value.to_date : null,
      };
      
      let resp = await this.adminService.getData('getSalaries', reqData);          
      if(resp && resp.responseCode === 1){
        this.list = resp.list;
        this.totalCount = (resp.list).length;
      }
    } catch (error) {
      this.list = '';
    }    
  }

  exportToExcel = async() => {
    if(this.list.length > 0){
      let sheetName = 'Salaries';
      let fileName = 'Salaries-' + new Date().getTime();
      let list = this.list.map((data: any) => {
        let info = {
          'Employee Name': data.employee_name,
          'Amount': data.salary_amount,
          'Salary Date': data.salary_date,
          'Transaction ID': data.transaction_number,
          'ESI': data.esi,
          'PF': data.pf,
          'HRA': data.hra,
          'Description': data.description,
          'Created On': new Date(data.createdAt).toLocaleString(),
          'Last Updated On': new Date(data.updatedAt).toLocaleString(),
        };
        return info;
      });
      await exportToExcel(list, fileName, sheetName);
    }
  }

  openActionDropDown = (i: any) => {       
    let dropdowns = document.getElementsByClassName("dropdown-menu");
    for (let i = 0; i < dropdowns.length; i++) {
      var openDropdown = dropdowns[i];
      if (openDropdown.classList.contains('show')) {
        openDropdown.classList.remove('show');
      }
    } 
    document.getElementById("actionDropDown-" + i).classList.toggle("show");
  }

  addFormSubmit = async() => {
    try {
      if(this.userAccess.view){
        this.submitted = true;
        // stop here if form is invalid
        if (this.addForm.invalid) {
          this.submitted = false;
            return;
        }
        const resp = await this.adminService.addOrUpdateData('addSalaries', this.addForm.value);
        if(resp && resp.responseCode === 1){
          this.getSalaries();
          await successAlert("Details added successfully")
        }else if(resp && resp.responseCode === 0){
          await errorAlert(resp.message);
        }else{
          await errorAlert("Details adding has failed ! Please try again");
        }
      }else{
        await errorAlert("Access Forbidden ! Please contact administrator");
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }    
    this.submitted = false;
    this.addForm.reset();
    document.getElementById("close_add_modal")?.click()
  }

  updateFormSubmit = async() => {
    try {
      if(this.userAccess.edit){
        this.submitted = true;
        // stop here if form is invalid
        if (this.updateForm.invalid) {
          this.submitted = false;
            return;
        }
        const updateResp = await this.adminService.addOrUpdateData('updateSalaries', this.updateForm.value);
        if(updateResp && updateResp.responseCode === 1){
          this.getSalaries();
          await successAlert("Details updated successfully")
        }else if(updateResp && updateResp.responseCode === 0){
          await errorAlert(updateResp.message);
        }else{
          await errorAlert("Details updating has failed ! Please try again");
        }
      }else{
        await errorAlert("Access Forbidden ! Please contact administrator");
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }    
    this.submitted = false;
    document.getElementById("close_edit_modal")?.click()
  }

  delete = async(id: String) => {
    try {
      if(this.userAccess.delete){
        if(await deleteConfirmAlert('Want to delete this')){
          const deleteResp = await this.adminService.deleteData('deleteSalaries', id);
          if(deleteResp && deleteResp.responseCode === 1){
            this.getSalaries();
            await successAlert("Details deleted successfully")
          }else{
            await errorAlert("Details deleting has failed ! Please try again")
          }
        }
      }else{
        await errorAlert("Access Forbidden ! Please contact administrator");
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }    
  }

}