import { Component, OnInit, TemplateRef, ViewEncapsulation } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { fileRegx, alnumRegx, alnumSpecialRegx, nonHTMLRegx } from '../../helpers/regExp';
import { AdminService } from '../../services/admin/admin.service';
import { successAlert, errorAlert, deleteConfirmAlert, changeConfirmAlert } from '../../helpers/sweetalert';
import { AccessService } from '../../services/access/access.service';
import { NbDialogService } from '@nebular/theme';

@Component({
  selector: 'ngx-commodity-list',
  templateUrl: './commodity-list.component.html',
  styleUrls: ['./commodity-list.component.scss']
})
export class CommodityListComponent implements OnInit {

  addForm!: FormGroup;
  updateForm!: FormGroup;
  uploadForm!: FormGroup;
  showModal! : boolean;
  submitted = false;
  list!: any;
  userAccess = {view: true, edit: false, delete: false};
  page: number = 1;
  pageSize: number = 25;
  totalCount: number = 0;
  search_string: string = '';
  pageSizes = ['25', '50', '100'];
  selectedFile: any;

  constructor(
    private formBuilder: FormBuilder,
    private adminService: AdminService,
    private accessService: AccessService,
    private dialogService: NbDialogService,
  ) { }

  ngOnInit(): void {
    const accessData = this.accessService.getActionAccess('commodity-list');
    if(accessData && accessData.length > 0){
      this.userAccess.edit = accessData[0].edit;
      this.userAccess.delete = accessData[0].delete;
    }
    this.addForm = this.formBuilder.group({
      product_name: ['', [Validators.required, Validators.pattern(nonHTMLRegx), Validators.maxLength(250)]],
      hsn_code: ['', [Validators.required, Validators.pattern(alnumRegx)]],
    });
    this.updateForm = this.formBuilder.group({
      id: ['', [Validators.required]],
      product_name: ['', [Validators.required, Validators.pattern(nonHTMLRegx), Validators.maxLength(250)]],
      hsn_code: ['', [Validators.required, Validators.pattern(alnumRegx)]],
    });
    this.uploadForm = this.formBuilder.group({
      commodity_file: ['', [Validators.required, Validators.pattern(fileRegx)]]
    });

    this.getCommodityList();
  }

  get form(){ return this.addForm.controls }
  get editForm(){ return this.updateForm.controls }
  get upload_form(){ return this.uploadForm.controls }

  openModal(dialog: TemplateRef<any>) {
    this.dialogService.open(
      dialog,
      {
        context: '',
        closeOnBackdropClick: false,
      });
  }

  edit = (data: any, modalVal: any) => {
    const updateData = {
      id: data?.id,
      product_name: data?.product_name,
      hsn_code: data?.hsn_code
    }
    this.updateForm.setValue(updateData);
    this.openModal(modalVal);
  }

  getCommodityList = async() => {
    let resp = await this.adminService.getData('getCommodityList');
    if(resp && resp.commodity_list){
      this.list = resp.commodity_list;
    }
  }

  changePageSize = (event: any) => {    
    this.pageSize = event.target.value;
  }

  onFileSelect = async(event: any) => {
    this.selectedFile = event.target.files;
  }

  downloadSampleFile = async() => {
    if(await changeConfirmAlert('Want to download sample file')){
      this.adminService.downLoadFiles('commodity-list', 'sample-commodity-file.csv');
    }    
  }

  addFormSubmit = async() => {
    try {
      this.submitted = true;
      // stop here if form is invalid
      if(this.addForm.invalid){
        this.submitted = false;
        return;
      }      
      const addResp = await this.adminService.addOrUpdateData('addCommodity', this.addForm.value);
      if(addResp && addResp.responseCode === 1){
        await this.getCommodityList();
        await successAlert('Details added successfully');
      } else if(addResp && addResp.responseCode === 0){
        await errorAlert(addResp.message);
      }else{
        await errorAlert('Something went wrong ! Please try again.');
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }
    this.submitted = false;
    this.addForm.reset();
    document.getElementById("close_add_modal")?.click();    
  }

  uploadFormSubmit = async() => {
    try {
      this.submitted = true;
      // stop here if form is invalid
      if(this.uploadForm.invalid){
        this.submitted = false;
        return;
      }
      let formData = new FormData;
      formData.append('file_path', 'commodity-list');
      formData.append("commodity_file", this.selectedFile[0], this.selectedFile[0]['name']);
      const addResp = await this.adminService.addOrUpdateData('uploadCommodities', formData);
      if(addResp && addResp.responseCode === 1){
        this.selectedFile = '';
        await this.getCommodityList();
        await successAlert('Details uploaded successfully');
      } else if(addResp && addResp.responseCode === 0){
        await errorAlert(addResp.message);
      }else{
        await errorAlert('Something went wrong ! Please try again.');
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }
    this.submitted = false;
    this.uploadForm.reset();
    document.getElementById("close_add_modal")?.click();    
  }

  updateFormSubmit = async() => {
    try {
      this.submitted = true;
      // stop here if form is invalid
      if(this.updateForm.invalid){
        this.submitted = false;
        return;
      }
      const updateResp = await this.adminService.addOrUpdateData('updateCommodity', this.updateForm.value);
      if(updateResp && updateResp.responseCode === 1){
        await this.getCommodityList();
        await successAlert('Details updated successfully');
        this.updateForm.reset();
      } else if(updateResp && updateResp.responseCode === 0){
        await errorAlert(updateResp.message);
      }else{
        await errorAlert('Something went wrong ! Please try again.');
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }
    this.submitted = false;
    this.updateForm.reset();
    document.getElementById("close_edit_modal")?.click();    
  }

  delete = async(id: string) =>{
    try {
      if(await deleteConfirmAlert('Want to delete this.')){
        const deleteResp = await this.adminService.deleteData('deleteCommodity', id);
        if(deleteResp && deleteResp.responseCode === 1){
          await this.getCommodityList();
          await successAlert("Details deleted successfully");
        }else{
          await errorAlert("Details deleting has failed ! Please try again");
        }
      }
    } catch (error) {
      await errorAlert("Something went wrong ! Please try again");
    }
  }

  deleteAll = async() =>{
    try {
      if(await deleteConfirmAlert('Want to delete all commodities.')){
        const deleteResp = await this.adminService.addOrUpdateData('deleteAllCommodities', {});
        if(deleteResp && deleteResp.responseCode === 1){
          await this.getCommodityList();
          await successAlert("Details are deleted successfully");
        }else{
          await errorAlert("Details deleting has failed ! Please try again");
        }
      }
    } catch (error) {
      await errorAlert("Something went wrong ! Please try again");
    }
  }

}
