import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { alnumSpecialRegx, alphaWithoutSpaceRegx } from '../../helpers/regExp';
import { PricesService } from '../../services/prices/prices.service';
import { successAlert, errorAlert, deleteConfirmAlert } from '../../helpers/sweetalert';
import { AccessService } from '../../services/access/access.service';

@Component({
  selector: 'ngx-countries',
  templateUrl: './countries.component.html',
  styleUrls: ['./countries.component.scss']
})
export class CountriesComponent implements OnInit {

  addForm!: FormGroup;
  updateForm!: FormGroup;
  submitted = false;
  list!: any;
  userAccess = {view: true, edit: false, delete: false};
  page: number = 1;
  pageSize: number = 25;
  totalCount: number = 0;
  search_string: string = '';
  pageSizes = ['25', '50', '100'];

  constructor(
    private formBuilder: FormBuilder,
    private pricesService: PricesService,
    private accessService: AccessService
  ) { }

  ngOnInit(): void {
    const accessData = this.accessService.getActionAccess('countries');
    if(accessData && accessData.length > 0){
      this.userAccess.edit = accessData[0].edit;
      this.userAccess.delete = accessData[0].delete;
    }
    this.addForm = this.formBuilder.group({
      country_name: ['', [Validators.required, Validators.pattern(alnumSpecialRegx)]],
      country_code: ['', [Validators.required, Validators.pattern(alphaWithoutSpaceRegx), Validators.max(2)]]
    });
    this.updateForm = this.formBuilder.group({
      country_id: ['', [Validators.required]],
      country_name: ['', [Validators.required, Validators.pattern(alnumSpecialRegx)]],
      country_code: ['', [Validators.required, Validators.pattern(alphaWithoutSpaceRegx), Validators.maxLength(2)]]
    });

    this.getCountries();
  }

  get form(){ return this.addForm.controls }
  get editForm(){ return this.updateForm.controls }

  edit = (data: any) => {
    const updateData = {
      country_id: data?.id,
      country: data?.country_name,
      code: data?.country_code,
    }
    this.updateForm.setValue(updateData);
  }

  uploadCountries = async() => {
    // const resp = await this.pricesService.uploadCountries();
    // console.log('uploadCountries resp', resp);    
  }

  getCountries = async() => {
    const countriesResp = await this.pricesService.getData('getCountries');
    this.list = countriesResp.countries;
    this.totalCount = (countriesResp.countries).length;
  }

  changePageSize = (event: any) => {    
    this.pageSize = event.target.value;
  }

  addFormSubmit = async() => {
    try {
      this.submitted = true;
      // stop here if form is invalid
      if(this.addForm.invalid){
        this.submitted = false;
        return;
      }
      const addResp = await this.pricesService.addOrUpdateData('addCountry', this.addForm.value);
      if(addResp && addResp.responseCode === 1){
        await this.getCountries();
        await successAlert('Country added successfully');
      } else if(addResp && addResp.responseCode === 0){
        await errorAlert(addResp.message);
      }else{
        await errorAlert('Something went wrong ! Please try again.');
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }
    this.submitted = false;
    this.addForm.reset();
    document.getElementById("close_add_modal")?.click();    
  }

  updateFormSubmit = async() => {
    try {
      this.submitted = true;
      // stop here if form is invalid
      if(this.updateForm.invalid){
        this.submitted = false;
        return;
      }
      const updateResp = await this.pricesService.addOrUpdateData('addCountry', this.updateForm.value);
      if(updateResp && updateResp.responseCode === 1){
        await this.getCountries();
        await successAlert('Country updated successfully');
        this.updateForm.reset();
      } else if(updateResp && updateResp.responseCode === 0){
        await errorAlert(updateResp.message);
      }else{
        await errorAlert('Something went wrong ! Please try again.');
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }
    this.submitted = false;
    this.updateForm.reset();
    document.getElementById("close_edit_modal")?.click();    
  }

  delete = async(countryID: string) =>{
    try {
      if(await deleteConfirmAlert('If you delete this country, state and cities associated with this country will also deleted. Want to delete this country.')){
        const deleteResp = await this.pricesService.deleteData('deleteCountry' ,countryID);
        if(deleteResp && deleteResp.responseCode === 1){
          await this.getCountries();
          await successAlert("Country deleted successfully");
        }else{
          await errorAlert("Country deleting has failed ! Please try again");
        }
      }
    } catch (error) {
      await errorAlert("Something went wrong ! Please try again");
    }
  }

}