import { Component, OnInit, TemplateRef, ViewEncapsulation } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { fileRegx, alnumRegx } from '../../helpers/regExp';
import { PricesService } from '../../services/prices/prices.service';
import { AdminService } from '../../services/admin/admin.service';
import { successAlert, errorAlert, deleteConfirmAlert, changeConfirmAlert } from '../../helpers/sweetalert';
import { AccessService } from '../../services/access/access.service';
import { NbDialogService } from '@nebular/theme';

@Component({
  selector: 'ngx-carrier-zones',
  templateUrl: './carrier-zones.component.html',
  styleUrls: ['./carrier-zones.component.scss'],
})
export class CarrierZonesComponent implements OnInit {

  addForm!: FormGroup;
  updateForm!: FormGroup;
  uploadForm!: FormGroup;
  showModal! : boolean;
  submitted = false;
  carriersList!: any;
  countriesList!: any;
  carrierZonesList!: any;
  carrierID!: any;
  userAccess = {view: true, edit: false, delete: false};
  page: number = 1;
  pageSize: number = 25;
  totalCount: number = 0;
  search_string: string = '';
  pageSizes = ['25', '50', '100'];
  selectedFile: any;

  constructor(
    private formBuilder: FormBuilder,
    private pricesService: PricesService,
    private accessService: AccessService,
    private adminService: AdminService,
    private dialogService: NbDialogService,
  ) { }

  ngOnInit(): void {
    const accessData = this.accessService.getActionAccess('carrier-zones');
    if(accessData && accessData.length > 0){
      this.userAccess.edit = accessData[0].edit;
      this.userAccess.delete = accessData[0].delete;
    }
    this.addForm = this.formBuilder.group({
      zone: ['', [Validators.required, Validators.pattern(alnumRegx), Validators.maxLength(5)]],
      country: ['', [Validators.required]],
    });
    this.updateForm = this.formBuilder.group({
      id: ['', [Validators.required]],
      zone: ['', [Validators.required, Validators.pattern(alnumRegx), Validators.maxLength(5)]],
      country: ['', [Validators.required]],
    });
    this.uploadForm = this.formBuilder.group({
      zones_file: ['', [Validators.required, Validators.pattern(fileRegx)]]
    });

    this.getCarriers();
    this.getCountries();
  }

  get form(){ return this.addForm.controls }
  get editForm(){ return this.updateForm.controls }
  get upload_form(){ return this.uploadForm.controls }

  openModal(dialog: TemplateRef<any>) {
    this.dialogService.open(
      dialog,
      {
        context: '',
        closeOnBackdropClick: false,
      });
  }

  edit = (data: any, modalVal: any) => {
    const updateData = {
      id: data?.id,
      zone: data?.zone,
      country: data?.country_code
    }
    this.updateForm.setValue(updateData);
    this.openModal(modalVal);
  }

  getCarriers = async() => {
    let resp = await this.pricesService.getData('getCarriers');
    if(resp && resp.carriers){
      this.carriersList = resp.carriers;
    }
  }

  getCountries = async() => {
    let resp = await this.pricesService.getData('getCountries');
    if(resp && resp.countries){
      this.countriesList = resp.countries;
    }
  }

  getCarrierZones = async(carrierID: any = this.carrierID) => {
    let resp = await this.pricesService.getData('getCarrierZones', {carrier_id: carrierID});
    if(resp && resp.carrier_zones){
      this.carrierZonesList = resp.carrier_zones;
    }
  }

  handleCarrierChange = (carrierID: any) => {  
    this.carrierID = carrierID;
    this.getCarrierZones(carrierID);    
  }

  changePageSize = (event: any) => {    
    this.pageSize = event.target.value;
  }

  onFileSelect = async(event: any) => {
    this.selectedFile = event.target.files;
  }

  downloadSampleFile = async() => {
    if(await changeConfirmAlert('Want to download sample file')){
      this.adminService.downLoadFiles('carrier-zones', 'sample-carrier-zones-file.csv');
    }    
  }

  addFormSubmit = async() => {
    try {
      this.submitted = true;
      // stop here if form is invalid
      if(this.addForm.invalid){
        this.submitted = false;
        return;
      }
      let countryValues = this.countriesList.filter((country: any) => country.country_code === this.addForm.value.country);
      let reqData = {
        zone: this.addForm.value.zone,
        carrier_id: this.carrierID,
        country_name: countryValues[0].country_name,
        country_code: this.addForm.value.country
      };
      const addResp = await this.pricesService.addOrUpdateData('addCarrierZone', reqData);
      if(addResp && addResp.responseCode === 1){
        await this.getCarrierZones();
        await successAlert('Carrier zone added successfully');
      } else if(addResp && addResp.responseCode === 0){
        await errorAlert(addResp.message);
      }else{
        await errorAlert('Something went wrong ! Please try again.');
      }
    } catch (err) {
      let msg = err.message ? err.message : 'Something went wrong ! Please try again.';
      await errorAlert(msg);
    }
    this.submitted = false;
    this.addForm.reset();
    document.getElementById("close_add_modal")?.click();    
  }

  uploadFormSubmit = async() => {
    try {
      this.submitted = true;
      // stop here if form is invalid
      if(this.uploadForm.invalid){
        this.submitted = false;
        return;
      }
      let formData = new FormData;
      formData.append('carrier_id', this.carrierID);
      formData.append('file_path', 'carrier-zones');
      formData.append("carrier_zones_file", this.selectedFile[0], this.selectedFile[0]['name']);
      
      const addResp = await this.pricesService.addOrUpdateData('uploadCarrierZones', formData);
      if(addResp && addResp.responseCode === 1){
        this.selectedFile = '';
        await this.getCarrierZones();
        await successAlert('Carrier zones uploaded successfully');
      } else if(addResp && addResp.responseCode === 0){
        await errorAlert(addResp.message);
      }else{
        await errorAlert('Something went wrong ! Please try again.');
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }
    this.submitted = false;
    this.uploadForm.reset();
    document.getElementById("close_add_modal")?.click();    
  }

  updateFormSubmit = async() => {
    try {
      this.submitted = true;
      // stop here if form is invalid
      if(this.updateForm.invalid){
        this.submitted = false;
        return;
      }
      let countryValues = this.countriesList.filter((country: any) => country.country_code === this.updateForm.value.country);
      let reqData = {
        id: this.updateForm.value.id,
        zone: this.updateForm.value.zone,
        carrier_id: this.carrierID,
        country_name: countryValues[0].country_name,
        country_code: this.updateForm.value.country
      };
      const updateResp = await this.pricesService.addOrUpdateData('updateCarrierZone', reqData);
      if(updateResp && updateResp.responseCode === 1){
        await this.getCarrierZones();
        await successAlert('Carrier zone updated successfully');
        this.updateForm.reset();
      } else if(updateResp && updateResp.responseCode === 0){
        await errorAlert(updateResp.message);
      }else{
        await errorAlert('Something went wrong ! Please try again.');
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }
    this.submitted = false;
    this.updateForm.reset();
    document.getElementById("close_edit_modal")?.click();    
  }

  delete = async(id: string) =>{
    try {
      if(await deleteConfirmAlert('Want to delete this.')){
        const deleteResp = await this.pricesService.deleteData('deleteCarrierZone', id);
        if(deleteResp && deleteResp.responseCode === 1){
          await this.getCarrierZones();
          await successAlert("Carrier zone deleted successfully");
        }else{
          await errorAlert("Carrier zone deleting has failed ! Please try again");
        }
      }
    } catch (error) {
      await errorAlert("Something went wrong ! Please try again");
    }
  }

  deleteAll = async() =>{
    try {
      if(await deleteConfirmAlert('Want to delete all zones in this carrier.')){
        const deleteResp = await this.pricesService.deleteData('deleteAllCarrierZones', this.carrierID);
        if(deleteResp && deleteResp.responseCode === 1){
          await this.getCarrierZones();
          await successAlert("Zones are deleted successfully");
        }else{
          await errorAlert("Zones deleting has failed ! Please try again");
        }
      }
    } catch (error) {
      await errorAlert("Something went wrong ! Please try again");
    }
  }

}