import { Component, OnInit } from '@angular/core';
import { NbSidebarService } from '@nebular/theme';
import { AdminService } from '../../services/admin/admin.service';
import { AccessService } from '../../services/access/access.service';
import { ActivatedRoute, Router } from '@angular/router';
import { getSumByKey } from '../../helpers/common';

@Component({
  selector: 'ngx-view-shipment-details',
  templateUrl: './view-shipment-details.component.html',
  styleUrls: ['./view-shipment-details.component.scss']
})
export class ViewShipmentDetailsComponent implements OnInit {

  userAccess = {view: true, edit: false, delete: false};
  shipmentID: string;
  shipmentDetails: any = [];
  totalCommodityValue: any = 0;

  constructor(
    private adminService: AdminService,
    private accessService: AccessService,
    private sidebarService: NbSidebarService,
    private route: ActivatedRoute,
    private router: Router,
  ) { }

  ngOnInit(): void {
    this.shipmentID = this.route.snapshot.paramMap.get('id');
    const accessData = this.accessService.getActionAccess('shipments-list');
    if(accessData && accessData.length > 0 && this.shipmentID){
      if(!accessData[0].view){
        this.router.navigate(['/admin/pages/access-denied']);
      }
      this.userAccess.edit = accessData[0].edit;
      this.userAccess.delete = accessData[0].delete;
    }else{
      this.router.navigate(['/admin/pages/access-denied']);
    }
    
    this.toggleSidebar();   

    this.getShipmentDetails();
  }

  toggleSidebar(): boolean {
    this.sidebarService.toggle(true, 'menu-sidebar');
    return false;
  }

  getShipmentDetails = async() => {
    let resp = await this.adminService.getData('getShipmentDetails', {id: this.shipmentID});          
    if(resp && resp.responseCode === 1){
      this.shipmentDetails = resp.info;             
      this.shipmentDetails.commodity_list = Array.isArray(this.shipmentDetails.commodity_list) ? this.shipmentDetails.commodity_list : JSON.parse(this.shipmentDetails.commodity_list);
      this.shipmentDetails.boxes_details = Array.isArray(this.shipmentDetails.boxes_details) ? this.shipmentDetails.boxes_details : JSON.parse(this.shipmentDetails.boxes_details);
      this.totalCommodityValue = await getSumByKey(this.shipmentDetails.commodity_list, 'commodity_value');
    }else{
      this.router.navigate(['/admin/pages/dashboard']);
    }   
  }

}
