import { Component, OnInit, TemplateRef } from '@angular/core';
import { AbstractControlOptions, FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NbDialogService } from '@nebular/theme';
import { AdminService } from '../../services/admin/admin.service';
import { AccessService } from '../../services/access/access.service';
import { dateRegx, floatRegx } from '../../helpers/regExp';
import { deleteConfirmAlert, successAlert, errorAlert } from '../../helpers/sweetalert';
import { ActivatedRoute, Router } from '@angular/router';
import { CompareValidator } from '../../helpers/confirmed.validator';

@Component({
  selector: 'ngx-manage-branch-commissions',
  templateUrl: './manage-branch-commissions.component.html',
  styleUrls: ['./manage-branch-commissions.component.scss']
})
export class ManageBranchCommissionsComponent implements OnInit {

  addForm!: FormGroup;
  updateForm!: FormGroup;
  showModal! : boolean;
  submitted = false;
  list: any;
  userAccess = {view: true, edit: false, delete: false};
  page: number = 1;
  pageSize: number = 25;
  totalCount: number = 0;
  pageSizes = ['25', '50', '100', '200'];
  branchID: string;

  constructor(
    private formBuilder: FormBuilder,
    private adminService: AdminService,
    private accessService: AccessService,
    private dialogService: NbDialogService,
    private route: ActivatedRoute,
    private router: Router,    
  ) { }

  ngOnInit(): void {
    this.branchID = this.route.snapshot.paramMap.get('id');
    const accessData = this.accessService.getActionAccess('branches');
    if(accessData && accessData.length > 0 && this.branchID){
      if(!accessData[0].edit){
        this.router.navigate(['/admin/pages/access-denied']);
      }
      this.userAccess.edit = accessData[0].edit;
      this.userAccess.delete = accessData[0].delete;
    }else{
      this.router.navigate(['/admin/pages/access-denied']);
    }

    this.addForm = this.formBuilder.group({
      weight_from: ['', [Validators.required, Validators.pattern(floatRegx)]],
      weight_to: ['', [Validators.required, Validators.pattern(floatRegx)]],
      commission_amount: ['', [Validators.required, Validators.pattern(floatRegx)]],
      from_date: ['', [Validators.required, Validators.pattern(dateRegx)]],
      to_date: ['', [Validators.required, Validators.pattern(dateRegx)]]
    }, {
      validator: [
        CompareValidator('weight_from', 'weight_to'),
        // CompareValidator('from_date', 'to_date'),
      ]      
    } as AbstractControlOptions
    );
    this.updateForm = this.formBuilder.group({
      id: ['', [Validators.required]],
      weight_from: ['', [Validators.required, Validators.pattern(floatRegx)]],
      weight_to: ['', [Validators.required, Validators.pattern(floatRegx)]],
      commission_amount: ['', [Validators.required, Validators.pattern(floatRegx)]],
      from_date: ['', [Validators.required, Validators.pattern(dateRegx)]],
      to_date: ['', [Validators.required, Validators.pattern(dateRegx)]]
    }, {
      validator: [
        CompareValidator('weight_from', 'weight_to'),
        // CompareValidator('from_date', 'to_date'),
      ]      
    } as AbstractControlOptions
    );

    this.getBranchCommissions();
  }

  get form() { return this.addForm.controls; }
  get editForm() { return this.updateForm.controls; }

  edit = (data: any, modalVal: any) => {
    const updateData = {
      id: data.id,
      weight_from: data.weight_from,
      weight_to: data.weight_to,
      commission_amount: data.commission_amount,
      from_date: data.from_date,
      to_date: data.to_date,
    }
    this.updateForm.setValue(updateData);
    this.openModal(modalVal);
  }

  changePageSize = (event: any) => {    
    this.pageSize = event.target.value;
  }

  handleDateChange = (type = 'add') => {
    let fromDate: any;
    if(type === 'add'){
      this.addForm.controls['to_date'].setValue(null);
      fromDate = this.addForm.value.from_date;
    }else{
      this.updateForm.controls['to_date'].setValue(null);
      fromDate = this.updateForm.value.from_date;
    }
    if(fromDate){
      let now = new Date(fromDate);
      const futureDate = new Date(now.setDate(now.getDate()));
      let date: any = futureDate.toISOString().split('T');
      if(type === 'add'){
        document.getElementById('to_date1').setAttribute('min', date[0]);
      }else{
        document.getElementById('to_date2').setAttribute('min', date[0]);
      }
    }
  }

  openModal(dialog: TemplateRef<any>) {
    this.dialogService.open(
      dialog,
      {
        context: '',
        closeOnBackdropClick: false,
      });
  }

  getBranchCommissions = async() => {
    let resp = await this.adminService.getData('getBranchCommissions', {branch_id: this.branchID});          
    if(resp && resp.responseCode === 1){
      this.list = resp.list;
      this.totalCount = (resp.list).length;
    }   
  }

  openActionDropDown = (i: any) => {       
    let dropdowns = document.getElementsByClassName("dropdown-menu");
    for (let i = 0; i < dropdowns.length; i++) {
      var openDropdown = dropdowns[i];
      if (openDropdown.classList.contains('show')) {
        openDropdown.classList.remove('show');
      }
    } 
    document.getElementById("actionDropDown-" + i).classList.toggle("show");
  }

  addFormSubmit = async() =>{
    try {
      if(this.userAccess.edit){
        this.submitted = true;
        // stop here if form is invalid
        if (this.addForm.invalid) {
          this.submitted = false;
            return;
        }
        let reqData = {branch_id: this.branchID, ...this.addForm.value};
        const resp = await this.adminService.addOrUpdateData('addBranchCommission', reqData);
        if(resp && resp.responseCode === 1){
          this.getBranchCommissions();
          await successAlert("Details added successfully")
        }else if(resp && resp.responseCode === 0){
          await errorAlert(resp.message);
        }else{
          await errorAlert("Details adding has failed ! Please try again");
        }
      }else{
        await errorAlert("Access Forbidden ! Please contact administrator");
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }    
    this.submitted = false;
    this.addForm.reset();
    document.getElementById("close_add_modal")?.click()
  }

  updateFormSubmit = async() => {
    try {
      if(this.userAccess.edit){
        this.submitted = true;
        // stop here if form is invalid
        if (this.updateForm.invalid) {
          this.submitted = false;
            return;
        }
        const updateResp = await this.adminService.addOrUpdateData('updateBranchCommission', this.updateForm.value);
        if(updateResp && updateResp.responseCode === 1){
          this.getBranchCommissions();
          await successAlert("Details updated successfully")
        }else if(updateResp && updateResp.responseCode === 0){
          await errorAlert(updateResp.message);
        }else{
          await errorAlert("Details updating has failed ! Please try again");
        }
      }else{
        await errorAlert("Access Forbidden ! Please contact administrator");
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }    
    this.submitted = false;
    document.getElementById("close_edit_modal")?.click()
  }

  delete = async(id: String) => {
    try {
      if(this.userAccess.delete){
        if(await deleteConfirmAlert('Want to delete this user')){
          const deleteResp = await this.adminService.deleteData('deleteBranchCommission', id);
          if(deleteResp && deleteResp.responseCode === 1){
            this.getBranchCommissions();
            await successAlert("Details deleted successfully")
          }else{
            await errorAlert("Details deleting has failed ! Please try again")
          }
        }
      }else{
        await errorAlert("Access Forbidden ! Please contact administrator");
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }    
  }

}
