import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NbSidebarService } from '@nebular/theme';
import { AdminService } from '../../services/admin/admin.service';
import { AccessService } from '../../services/access/access.service';
import { AuthenticateService } from '../../services/authentication/authenticate.service';
import { getSumByKey, getSumByKeyWithFilter } from '../../helpers/common';

@Component({
  selector: 'ngx-branch-payment-details',
  templateUrl: './branch-payment-details.component.html',
  styleUrls: ['./branch-payment-details.component.scss']
})
export class BranchPaymentDetailsComponent implements OnInit {

  searchForm!: FormGroup;
  showModal! : boolean;
  userAccess = {view: true, edit: false, delete: false};
  page: number = 1;
  pageSize: number = 25;
  totalCount: number = 0;
  pageSizes = ['25', '50', '100', '200'];
  currentUserData: any;
  branchesList: any;
  transactionsList: any;
  isSuperAdmin: Boolean = true;
  branchID: any;
  currentDate: any;
  pastDate: any;
  paymentDetails: any;
  shipmentsList: any;
  shipmentsTotalCount: any = 0;
  totalCashAmount: any = 0;
  totalBankOnlineAmount: any = 0;

  constructor(
    private formBuilder: FormBuilder,
    private adminService: AdminService,
    private accessService: AccessService,
    private authenticateService: AuthenticateService,
    private sidebarService: NbSidebarService,
  ) { }

  ngOnInit(): void {
    const accessData = this.accessService.getActionAccess('branch-payment-details');
    if(accessData && accessData.length > 0){
      this.userAccess.edit = accessData[0].edit;
      this.userAccess.delete = accessData[0].delete;
    }

    this.currentDate = new Date().toISOString().split('T')[0];
    let now = new Date();
    const backDate = new Date(now.setDate(now.getDate() - 30));
    this.pastDate = backDate.toISOString().split('T')[0];
    
    // this.searchForm = this.formBuilder.group({
    //   from_date: [this.pastDate, Validators.required],
    //   to_date: [this.currentDate],
    //   branch: [],
    // });

    this.currentUserData = this.authenticateService.currentUserValue;
    if(this.currentUserData && this.currentUserData.role_type && this.currentUserData.role_type !== 'Super-Admin'){
      this.isSuperAdmin = false;
      this.branchID = this.currentUserData.branch_id;
      this.searchForm = this.formBuilder.group({
        from_date: [this.pastDate, Validators.required],
        to_date: [this.currentDate]
      });
      this.getBranchPaymentDetails();
    }else{
      this.searchForm = this.formBuilder.group({
        from_date: [this.pastDate, Validators.required],
        to_date: [this.currentDate],
        branch: [, [Validators.required]],
      });
    }

    this.getBranchesList();
    // this.toggleSidebar();
  }

  get form(){ return this.searchForm.controls; }

  toggleSidebar(): boolean {
    this.sidebarService.toggle(true, 'menu-sidebar');
    return false;
  }

  changePageSize = (event: any) => {    
    this.pageSize = event.target.value;
  }

  handleDateChange = (event: any) => {
    this.searchForm.controls['to_date'].setValue(null);
    let fromDate = this.searchForm.value.from_date;
    if(fromDate){
      let now = new Date(fromDate);
      const futureDate = new Date(now.setDate(now.getDate()));
      let date: any = futureDate.toISOString().split('T');
      document.getElementById('to_date').setAttribute('min', date[0]);
    }
  }

  getBranchPaymentDetails = async() => {
    try {
      this.branchID = this.searchForm && this.searchForm.value && this.searchForm.value.branch ? this.searchForm.value.branch : this.branchID;
      let reqData = {
        branch_id: this.branchID,
        from_date: this.searchForm.value.from_date ? this.searchForm.value.from_date : null,
        to_date: this.searchForm.value.to_date ? this.searchForm.value.to_date : null,
      };

      this.getBankTransactions();
      this.paymentDetails = '';
      let resp = await this.adminService.getData('getShipmentsList', reqData);          
      if(resp && resp.responseCode === 1){
        this.shipmentsList = resp.shipments;
        this.shipmentsTotalCount = (resp.shipments).length;
        let totalWeight = await getSumByKey(this.shipmentsList, 'chargable_weight');
        let totalBasicAmount = await getSumByKey(this.shipmentsList, 'basic_amount');
        let totalGSTAmount = await getSumByKey(this.shipmentsList, 'gst_amount');
        let totalAmount = await getSumByKey(this.shipmentsList, 'total_amount');
        let totalCardAmount = await getSumByKeyWithFilter(this.shipmentsList, 'total_amount', 'mode_of_payment', 'DEBIT/CREDIT CARD');
        let totalUPIAmount = await getSumByKeyWithFilter(this.shipmentsList, 'total_amount', 'mode_of_payment', 'UPI');
        let totalOtherAmount = await getSumByKeyWithFilter(this.shipmentsList, 'total_amount', 'mode_of_payment', 'OTHERS');
        let commisionsResp = await this.adminService.getData('getBranchCommissions', {branch_id: this.branchID});
        let commisionPaymentsResp = await this.adminService.getData('getBranchCommissionPayments', reqData);
        let totalCommissionPaymentPaid: any = 0;
        if(commisionPaymentsResp && commisionPaymentsResp.transactions){
          totalCommissionPaymentPaid = await getSumByKey(commisionPaymentsResp.transactions, 'amount');
        }
        let totalCommission: number = 0;
        if(commisionsResp && commisionsResp.list){
          this.shipmentsList.map((data:any) => {
            let filterData = commisionsResp.list.filter((data1: any) => data.date >= data1.from_date && data.date <= data1.to_date && data.chargable_weight >= data1.weight_from && data.chargable_weight <= data1.weight_to);
            totalCommission = filterData.length > 0 ? totalCommission + (parseFloat(filterData[0].commission_amount) * parseFloat(data.chargable_weight)) : 0;
          });
        }
        this.paymentDetails = {
          shipmentsTotalCount: this.shipmentsTotalCount,
          totalWeight: totalWeight,
          totalBasicAmount: totalBasicAmount,
          totalGSTAmount: totalGSTAmount,
          totalAmount: totalAmount,
          totalCardAmount: totalCardAmount,
          totalUPIAmount: totalUPIAmount,
          totalOtherAmount: totalOtherAmount,
          totalCommission: totalCommission,
          totalCommissionPaymentPaid: totalCommissionPaymentPaid,
          totalCommissionDue: totalCommission - totalCommissionPaymentPaid,
        }
      }
    } catch (error) {
      this.paymentDetails = '';
    }    
  }

  getBankTransactions = async() => {
    try {
      this.branchID = this.searchForm && this.searchForm.value && this.searchForm.value.branch ? this.searchForm.value.branch : this.branchID;
      let reqData = {
        branch_id: this.branchID,
        from_date: this.searchForm.value.from_date ? this.searchForm.value.from_date : null,
        to_date: this.searchForm.value.branch ? this.searchForm.value.to_date : null,
        approved: true
      };
      
      let resp = await this.adminService.getData('getBankTransactions', reqData);          
      if(resp && resp.responseCode === 1){
        this.transactionsList = resp.transactions;
        this.totalCount = (resp.transactions).length;
        let cashTransactions:any = this.transactionsList.filter((data: any) => data.mode_of_deposit === 'CASH' && data.status === 'APPROVED');
        let onlineTransactions:any = this.transactionsList.filter((data: any) => data.mode_of_deposit !== 'CASH' && data.status === 'APPROVED');
        
        let totalCashAmount: any = 0;
        let totalBankOnlineAmount: any = 0;
        if(cashTransactions.length > 0){
          totalCashAmount = await getSumByKey(cashTransactions, 'amount');
        }
        if(onlineTransactions.length > 0){
          totalBankOnlineAmount = await getSumByKey(onlineTransactions, 'amount');
        }
        this.totalCashAmount = totalCashAmount;
        this.totalBankOnlineAmount = totalBankOnlineAmount;
      }
    } catch (error) {
      this.transactionsList = '';
    }    
  }

  getBranchesList = async() => {
    try {
      let resp = await this.adminService.getData('getBranches');          
      if(resp && resp.responseCode === 1){
        this.branchesList = resp.branchesList;
      }
    } catch (error) {
      this.branchesList = {};
    }    
  }

}