  ///////////// HANDLING DYNAMIC BOXES DETAILS /////////////
  handleNumberOfPiecesChange = () => {
    let no_of_pieces = parseInt(this.updateForm.value.no_of_pieces);
    if(this.boxesCount < no_of_pieces){
      for(let i=this.boxesCount; i < no_of_pieces; i++){
        this.addBoxDetails();
      }
    }else if(this.boxesCount > no_of_pieces){
      for(let i=this.boxesCount; i >= no_of_pieces; i--){
        this.removeBoxDetails(i);        
      }
    }
  }

  handleBoxDetailsChange = () => {
    let totalActualWeight: any = 0;
    let totalVolumetricWeight: any = 0;
    let totalChargeableWeight: any = 0;
    this.boxDetails.value.map((data: any, index: any) => {
      let boxWeight = data.box_weight;
      let width: number;
      let length: number;
      let height: number;
      if(boxWeight){
        if(boxWeight === '3KG'){
          width = 30;
          length = 21;
          height = 21;
        }else if(boxWeight === '5KG'){
          width = 36;
          length = 26;
          height = 26;
        }else if(boxWeight === '10KG'){
          width = 40;
          length = 35;
          height = 35;
        }else if(boxWeight === '15KG'){
          width = 60;
          length = 35;
          height = 35;
        }else if(boxWeight === 'CUSTOM'){
          width = data.width;
          length = data.length;
          height = data.height;
        }
      }
      this.getBoxDetailsFormGroup(index).controls['width'].setValue(width);
      this.getBoxDetailsFormGroup(index).controls['length'].setValue(length);
      this.getBoxDetailsFormGroup(index).controls['height'].setValue(height);
      let valumetricWeight: any = 0;
      let actualWeight: any = data.actual_weight ? parseFloat(data.actual_weight) : 0;
      if(width && length && height){
        valumetricWeight = (width*length*height)/5000;
      }
      let chargeableWeight = roundOffWeight(actualWeight > valumetricWeight ? actualWeight : valumetricWeight);
      this.getBoxDetailsFormGroup(index).controls['valumetric_weight'].setValue(roundOffWeight(valumetricWeight));
      this.getBoxDetailsFormGroup(index).controls['chargeable_weight'].setValue(chargeableWeight);
      
      totalActualWeight = totalActualWeight + actualWeight;
      totalVolumetricWeight = totalVolumetricWeight + valumetricWeight;
      totalChargeableWeight = totalChargeableWeight + chargeableWeight;
    });
    this.updateForm.controls['actual_weight'].setValue(totalActualWeight);
    this.updateForm.controls['valumetric_weight'].setValue(roundOffWeight(totalVolumetricWeight));
    this.updateForm.controls['chargable_weight'].setValue(roundOffWeight(totalChargeableWeight));
    this.calculatePrices();
  }

  calculatePrices = async() => {
    if(this.updateForm.value.no_of_pieces){
      let noOfPieces: number = this.noOfPieces = parseInt(this.updateForm.value.no_of_pieces);
      let medicineShipment = this.updateForm.value.medicine_shipment;
      let medicineShipmentValue = 'NO';
      if(medicineShipment && medicineShipment === 'YES' && noOfPieces === 1){
        medicineShipmentValue = 'YES'
      }
      this.updateForm.controls['medicine_shipment'].setValue(medicineShipmentValue);
      let finalWeight = this.updateForm.value.chargable_weight;      
      let priceData = this.pricesList && this.pricesList.length > 0 && this.pricesList.filter((data: any) => data.weight_from <= finalWeight && data.weight_to >= finalWeight);
      if(priceData && priceData.length > 0){
        let pricePerKG = medicineShipmentValue === 'YES' ? priceData[0].medicine_price_per_kg : priceData[0].price_per_kg;
        let basicAmount = pricePerKG * finalWeight;
        let commercialChargesAmount = this.updateForm.value.commercial_charges_amount ? parseInt(this.updateForm.value.commercial_charges_amount) : 0;
        let jewelleryAppraisalAmount = this.updateForm.value.jewellery_appraisal_amount ? parseInt(this.updateForm.value.jewellery_appraisal_amount) : 0;
        let pickupChargesAmount = this.updateForm.value.pickup_charges_amount ? parseInt(this.updateForm.value.pickup_charges_amount) : 0;
        let packingChargesAmount = this.updateForm.value.packing_charges_amount ? parseInt(this.updateForm.value.packing_charges_amount) : 0;
        let priceAmount = basicAmount + commercialChargesAmount + jewelleryAppraisalAmount + pickupChargesAmount + packingChargesAmount;
        let gstAmount = (priceAmount*18)/100;
        let otherAmount = this.updateForm.value.other_amount ? parseInt(this.updateForm.value.other_amount) : 0;
        let totalAmount = Math.round(priceAmount + gstAmount + otherAmount);
        
        this.updateForm.controls['basic_amount'].setValue(basicAmount);
        this.updateForm.controls['commercial_charges_amount'].setValue(commercialChargesAmount);
        this.updateForm.controls['jewellery_appraisal_amount'].setValue(jewelleryAppraisalAmount);
        this.updateForm.controls['pickup_charges_amount'].setValue(pickupChargesAmount);
        this.updateForm.controls['packing_charges_amount'].setValue(packingChargesAmount);
        this.updateForm.controls['gst_amount'].setValue(gstAmount);
        this.updateForm.controls['total_amount'].setValue(totalAmount);
      }
    }
  }

  handlePriceChange = () => {
    let basicAmount = parseInt(this.updateForm.value.basic_amount);
    let commercialChargesAmount = parseInt(this.updateForm.value.commercial_charges_amount);
    let jewelleryAppraisalAmount = parseInt(this.updateForm.value.jewellery_appraisal_amount);
    let pickupChargesAmount = parseInt(this.updateForm.value.pickup_charges_amount);
    let packingChargesAmount = parseInt(this.updateForm.value.packing_charges_amount);
    let gstAmount = parseInt(this.updateForm.value.gst_amount);
    let otherAmount = this.updateForm.value.other_amount ? parseInt(this.updateForm.value.other_amount) : 0;
    let totalAmount = basicAmount + commercialChargesAmount + jewelleryAppraisalAmount + pickupChargesAmount + packingChargesAmount + gstAmount + otherAmount;
    this.updateForm.controls['other_amount'].setValue(otherAmount);
    this.updateForm.controls['total_amount'].setValue(totalAmount);
  }

