import {Component, OnInit} from '@angular/core';
import { AccessService } from '../../services/access/access.service';
import { AdminService } from '../../services/admin/admin.service';
import { AuthenticateService } from '../../services/authentication/authenticate.service';

@Component({
  selector: 'ngx-dashboard',
  styleUrls: ['./dashboard.component.scss'],
  templateUrl: './dashboard.component.html',
})
export class DashboardComponent implements OnInit {
  
  currentUserData: any;
  isSuperAdmin: boolean = false;
  totalBranchesCount: number = 0;
  totalUsersCount: number = 0;
  totalShipmentsWithTrackingNoCount: number = 0;
  totalShipmentsWithoutTrackingNoCount: number = 0;
  totalStockPendingRequestsCount: number = 0;
  totalPendingComplaintsCount: number = 0;
  totalClosedComplaintsCount: number = 0;
  userAccess: any;
  totalActiveUsersCount: number = 0;
  totalInActiveUsersCount: number = 0;

  constructor(
    private adminService: AdminService,
    private authenticateService: AuthenticateService,
    private accessService: AccessService,
  ) {}

  ngOnInit(){
    this.currentUserData = this.authenticateService.currentUserValue;
    if(this.currentUserData && this.currentUserData.role_type && this.currentUserData.role_type === 'Super-Admin'){
      this.isSuperAdmin = true;
    }

    const branchesAccessData = this.accessService.getActionAccess('branches');
    const usersAccessData = this.accessService.getActionAccess('users');
    const shipmentsAccessData = this.accessService.getActionAccess('shipments-list');
    const stockRequestsAccessData = this.accessService.getActionAccess('stock-requests');
    const complaintsAccessData = this.accessService.getActionAccess('complaints');
    this.userAccess = {
      branches: branchesAccessData.length > 0 ? true : false,
      users: usersAccessData.length > 0 ? true : false,
      shipmentsList: shipmentsAccessData.length > 0 ? true : false,
      stockRequests: stockRequestsAccessData.length > 0 ? true : false,
      complaints: complaintsAccessData.length > 0 ? true : false
    }

    branchesAccessData.length > 0 && this.getBranchesCount();
    usersAccessData.length > 0 && this.getUsersCount();
    shipmentsAccessData.length > 0 && this.getShipmentsCount();
    stockRequestsAccessData.length > 0 && this.getStockRequestsCount();
    complaintsAccessData.length > 0 && this.getComplaintsCount();
  }

  getBranchesCount = async() => {
    try {
      let resp = await this.adminService.getData('getBranchesCount');          
      if(resp && resp.responseCode === 1 && resp.count){
        this.totalBranchesCount = resp.count;
      }
    } catch (error) {
      this.totalBranchesCount = 0;
    }    
  }

  getUsersCount = async() => {
    try {
      let resp = await this.adminService.getData('getUsersCount');          
      if(resp && resp.responseCode === 1 && resp.count){
        this.totalUsersCount = resp.count.totalUsers;
        this.totalActiveUsersCount = resp.count.activeUsers;
        this.totalInActiveUsersCount = resp.count.totalUsers - resp.count.activeUsers;
      }
    } catch (error) {
      this.totalUsersCount = 0;
    }    
  }

  getShipmentsCount = async() => {
    try {
      let resp = await this.adminService.getData('getShipmentsCount');          
      if(resp && resp.responseCode === 1 && resp.countData){
        this.totalShipmentsWithTrackingNoCount = resp.countData.shipmentsWithTrackingNoCount;
        this.totalShipmentsWithoutTrackingNoCount = resp.countData.shipmentsWithoutTrackingNoCount;
      }
    } catch (error) {

    }    
  }

  getStockRequestsCount = async() => {
    try {
      let resp = await this.adminService.getData('getStockRequestsCount');          
      if(resp && resp.responseCode === 1 && resp.countData){
        this.totalStockPendingRequestsCount = resp.countData.pendingStockCount;
      }
    } catch (error) {

    }    
  }

  getComplaintsCount = async() => {
    try {
      let resp = await this.adminService.getData('getComplaintsCount');          
      if(resp && resp.responseCode === 1 && resp.countData){
        this.totalPendingComplaintsCount = resp.countData.pendingCount;
        this.totalClosedComplaintsCount = resp.countData.closedCount;
      }
    } catch (error) {

    }    
  }

}
