import { Component, OnInit, TemplateRef } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { fileRegx, alnumRegx, floatRegx } from '../../helpers/regExp';
import { PricesService } from '../../services/prices/prices.service';
import { AdminService } from '../../services/admin/admin.service';
import { successAlert, errorAlert, deleteConfirmAlert, changeConfirmAlert } from '../../helpers/sweetalert';
import { AccessService } from '../../services/access/access.service';
import { NbDialogService } from '@nebular/theme';

@Component({
  selector: 'ngx-prices',
  templateUrl: './prices.component.html',
  styleUrls: ['./prices.component.scss']
})
export class PricesComponent implements OnInit {

  addForm!: FormGroup;
  updateForm!: FormGroup;
  uploadForm!: FormGroup;
  showModal! : boolean;
  submitted = false;
  carriersList!: any;
  list!: any;
  carrierID!: any;
  userAccess = {view: true, edit: false, delete: false};
  page: number = 1;
  pageSize: number = 25;
  totalCount: number = 0;
  search_string: string = '';
  pageSizes = ['25', '50', '100'];
  selectedFile: any;

  constructor(
    private formBuilder: FormBuilder,
    private pricesService: PricesService,
    private adminService: AdminService,
    private accessService: AccessService,
    private dialogService: NbDialogService,
  ) { }

  ngOnInit(): void {
    const accessData = this.accessService.getActionAccess('prices');
    if(accessData && accessData.length > 0){
      this.userAccess.edit = accessData[0].edit;
      this.userAccess.delete = accessData[0].delete;
    }
    this.addForm = this.formBuilder.group({
      zone: ['', [Validators.required, Validators.pattern(alnumRegx), Validators.maxLength(5)]],
      weight_from: ['', [Validators.required, Validators.pattern(floatRegx)]],
      weight_to: ['', [Validators.required, Validators.pattern(floatRegx)]],
      price_per_kg: ['', [Validators.required, Validators.pattern(floatRegx)]],
      medicine_price_per_kg: ['', [Validators.pattern(floatRegx)]],
    });
    this.updateForm = this.formBuilder.group({
      id: ['', [Validators.required]],
      zone: ['', [Validators.required, Validators.pattern(alnumRegx), Validators.maxLength(5)]],
      weight_from: ['', [Validators.required, Validators.pattern(floatRegx)]],
      weight_to: ['', [Validators.required, Validators.pattern(floatRegx)]],
      price_per_kg: ['', [Validators.required, Validators.pattern(floatRegx)]],
      medicine_price_per_kg: ['', [Validators.pattern(floatRegx)]],
    });
    this.uploadForm = this.formBuilder.group({
      prices_file: ['', [Validators.required, Validators.pattern(fileRegx)]]
    });

    this.getCarriers();
  }

  get form(){ return this.addForm.controls }
  get editForm(){ return this.updateForm.controls }
  get upload_form(){ return this.uploadForm.controls }

  openModal(dialog: TemplateRef<any>) {
    this.dialogService.open(
      dialog,
      {
        context: '',
        closeOnBackdropClick: false,
      });
  }

  edit = (data: any, modalVal: any) => {
    const updateData = {
      id: data?.id,
      zone: data?.zone,
      weight_from: data?.weight_from,
      weight_to: data?.weight_to,
      price_per_kg: data?.price_per_kg,
      medicine_price_per_kg: data?.medicine_price_per_kg
    }
    this.updateForm.setValue(updateData);
    this.openModal(modalVal);
  }

  getCarriers = async() => {
    let resp = await this.pricesService.getData('getCarriers');
    if(resp && resp.carriers){
      this.carriersList = resp.carriers;
    }
  }

  getPrices = async(carrierID: any = this.carrierID) => {
    let resp = await this.pricesService.getData('getPrices', {carrier_id: carrierID});
    if(resp && resp.prices){
      this.list = resp.prices;
    }
  }

  handleCarrierChange = (carrierID: any) => {  
    this.carrierID = carrierID;
    this.getPrices(carrierID);    
  }

  changePageSize = (event: any) => {    
    this.pageSize = event.target.value;
  }

  onFileSelect = async(event: any) => {
    this.selectedFile = event.target.files;
  }

  downloadSampleFile = async() => {
    if(await changeConfirmAlert('Want to download sample file')){
      this.adminService.downLoadFiles('prices', 'sample-prices-file.csv');
    }    
  }

  addFormSubmit = async() => {
    try {
      this.submitted = true;
      // stop here if form is invalid
      if(this.addForm.invalid){
        this.submitted = false;
        return;
      }
      let reqData = {
        ...this.addForm.value,
        carrier_id: this.carrierID
      };
      const addResp = await this.pricesService.addOrUpdateData('addPrices', reqData);
      if(addResp && addResp.responseCode === 1){
        await this.getPrices();
        await successAlert('Price added successfully');
      } else if(addResp && addResp.responseCode === 0){
        await errorAlert(addResp.message);
      }else{
        await errorAlert('Something went wrong ! Please try again.');
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }
    this.submitted = false;
    this.addForm.reset();
    document.getElementById("close_add_modal")?.click();    
  }

  uploadFormSubmit = async() => {
    try {
      this.submitted = true;
      // stop here if form is invalid
      if(this.uploadForm.invalid){
        this.submitted = false;
        return;
      }
      let formData = new FormData;
      formData.append('carrier_id', this.carrierID);
      formData.append('file_path', 'prices');
      formData.append("prices_file", this.selectedFile[0], this.selectedFile[0]['name']);
      
      const addResp = await this.pricesService.addOrUpdateData('uploadPrices', formData);
      if(addResp && addResp.responseCode === 1){
        this.selectedFile = '';
        await this.getPrices();
        await successAlert('Prices uploaded successfully');
      } else if(addResp && addResp.responseCode === 0){
        await errorAlert(addResp.message);
      }else{
        await errorAlert('Something went wrong ! Please try again.');
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }
    this.submitted = false;
    this.uploadForm.reset();
    document.getElementById("close_upload_modal")?.click();    
  }

  updateFormSubmit = async() => {
    try {
      this.submitted = true;
      // stop here if form is invalid
      if(this.updateForm.invalid){
        this.submitted = false;
        return;
      }
      let reqData = {
        ...this.updateForm.value,
        carrier_id: this.carrierID,
      };
      const updateResp = await this.pricesService.addOrUpdateData('updatePrice', reqData);
      if(updateResp && updateResp.responseCode === 1){
        await this.getPrices();
        await successAlert('Price details updated successfully');
        this.updateForm.reset();
      } else if(updateResp && updateResp.responseCode === 0){
        await errorAlert(updateResp.message);
      }else{
        await errorAlert('Something went wrong ! Please try again.');
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }
    this.submitted = false;
    this.updateForm.reset();
    document.getElementById("close_edit_modal")?.click();    
  }

  delete = async(data: any) =>{
    try {
      if(await deleteConfirmAlert('Want to delete this.')){
        const deleteResp = await this.pricesService.addOrUpdateData('deletePrice', {id: data.id, carrier_id: data.carrier_id});
        if(deleteResp && deleteResp.responseCode === 1){
          await this.getPrices();
          await successAlert("Price deleted successfully");
        }else{
          await errorAlert("Price deleting has failed ! Please try again");
        }
      }
    } catch (error) {
      await errorAlert("Something went wrong ! Please try again");
    }
  }

  deleteAll = async() =>{
    try {
      if(await deleteConfirmAlert('Want to delete all prices in this carrier.')){
        const deleteResp = await this.pricesService.deleteData('deleteAllPrices', this.carrierID);
        if(deleteResp && deleteResp.responseCode === 1){
          await this.getPrices();
          await successAlert("Prices are deleted successfully");
        }else{
          await errorAlert("Prices deleting has failed ! Please try again");
        }
      }
    } catch (error) {
      await errorAlert("Something went wrong ! Please try again");
    }
  }

}
