import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AdminService } from '../../services/admin/admin.service';
import { AccessService } from '../../services/access/access.service';
import { getSumByKeyWithFilter, getSumByKeyWithFilters } from '../../helpers/common';

@Component({
  selector: 'ngx-stock-availability',
  templateUrl: './stock-availability.component.html',
  styleUrls: ['./stock-availability.component.scss']
})
export class StockAvailabilityComponent implements OnInit {

  searchForm!: FormGroup;
  userAccess = {view: true, edit: false, delete: false};
  currentUserData: any;
  list: any;
  currentDate: any;
  pastDate: any;
  total3kgPurchaseCount: any = 0;
  total5kgPurchaseCount: any = 0;
  total10kgPurchaseCount: any = 0;
  total15kgPurchaseCount: any = 0;
  totalTapesPurchaseCount: any = 0;
  totalCoversPacketPurchaseCount: any = 0;
  totalBillBooksPurchaseCount: any = 0;
  total3kgApprovedCount: any = 0;
  total5kgApprovedCount: any = 0;
  total10kgApprovedCount: any = 0;
  total15kgApprovedCount: any = 0;
  totalTapesApprovedCount: any = 0;
  totalCoversPacketApprovedCount: any = 0;
  totalBillBooksApprovedCount: any = 0;
  stockRequestsList: any;
  stockPurchasesList: any;

  constructor(
    private formBuilder: FormBuilder,
    private adminService: AdminService,
    private accessService: AccessService,
  ) { }


  ngOnInit(): void {
    const accessData = this.accessService.getActionAccess('stock-availability');
    if(accessData && accessData.length > 0){
      this.userAccess.edit = accessData[0].edit;
      this.userAccess.delete = accessData[0].delete;
    }

    this.currentDate = new Date().toISOString().split('T')[0];
    let now = new Date();
    const backDate = new Date(now.setDate(now.getDate() - 30));
    this.pastDate = backDate.toISOString().split('T')[0];
    
    this.searchForm = this.formBuilder.group({
      from_date: [this.pastDate, Validators.required],
      to_date: [this.currentDate]
    });

    this.getStockAvailability();
  }

  handleDateChange = (event: any) => {
    this.searchForm.controls['to_date'].setValue(null);
    let fromDate = this.searchForm.value.from_date;
    if(fromDate){
      let now = new Date(fromDate);
      const futureDate = new Date(now.setDate(now.getDate()));
      let date: any = futureDate.toISOString().split('T');
      document.getElementById('to_date').setAttribute('min', date[0]);
    }
  }

  getStockAvailability = async() => {
    this.getStockPurchases();
    this.getStockRequests();
  }

  getStockPurchases = async() => {
    try {      
      let resp = await this.adminService.getData('getStockPurchases');          
      if(resp && resp.responseCode === 1){
        this.stockPurchasesList = resp.list;
        this.total3kgPurchaseCount = await getSumByKeyWithFilter(this.stockPurchasesList, 'quantity', 'box_weight', '3KGS');
        this.total5kgPurchaseCount = await getSumByKeyWithFilter(this.stockPurchasesList, 'quantity', 'box_weight', '5KGS');
        this.total10kgPurchaseCount = await getSumByKeyWithFilter(this.stockPurchasesList, 'quantity', 'box_weight', '10KGS');
        this.total15kgPurchaseCount = await getSumByKeyWithFilter(this.stockPurchasesList, 'quantity', 'box_weight', '15KGS');
        this.totalTapesPurchaseCount = await getSumByKeyWithFilter(this.stockPurchasesList, 'quantity', 'stock_type', 'TAPES');
        this.totalCoversPacketPurchaseCount = await getSumByKeyWithFilter(this.stockPurchasesList, 'quantity', 'stock_type', 'COVERS PACKET');
        this.totalBillBooksPurchaseCount = await getSumByKeyWithFilter(this.stockPurchasesList, 'quantity', 'stock_type', 'BILL BOOKS');
      }
    } catch (error) {
      this.stockPurchasesList = [];
    }    
  }

  getStockRequests = async() => {
    try {
      let reqData = {
        from_date: this.searchForm.value.from_date ? this.searchForm.value.from_date : null,
        to_date: this.searchForm.value.to_date ? this.searchForm.value.to_date : null,
      };

      let resp1 = await this.adminService.getData('getStockRequests', reqData);          
      if(resp1 && resp1.responseCode === 1){
        this.stockRequestsList = resp1.list;
        this.total3kgApprovedCount = await getSumByKeyWithFilters(this.stockRequestsList, 'approved_quantity', 'box_weight', '3KGS', 'status' , 'APPROVED');
        this.total5kgApprovedCount = await getSumByKeyWithFilters(this.stockRequestsList, 'approved_quantity', 'box_weight', '5KGS', 'status' , 'APPROVED');
        this.total10kgApprovedCount = await getSumByKeyWithFilters(this.stockRequestsList, 'approved_quantity', 'box_weight', '10KGS', 'status' , 'APPROVED');
        this.total15kgApprovedCount = await getSumByKeyWithFilters(this.stockRequestsList, 'approved_quantity', 'box_weight', '15KGS', 'status' , 'APPROVED');
        this.totalTapesApprovedCount = await getSumByKeyWithFilters(this.stockRequestsList, 'approved_quantity', 'stock_type', 'TAPES', 'status' , 'APPROVED');
        this.totalCoversPacketApprovedCount = await getSumByKeyWithFilters(this.stockRequestsList, 'approved_quantity', 'stock_type', 'COVERS PACKET', 'status' , 'APPROVED');
        this.totalBillBooksApprovedCount = await getSumByKeyWithFilters(this.stockRequestsList, 'approved_quantity', 'stock_type', 'BILL BOOKS', 'status' , 'APPROVED');
      }
    } catch (error) {
      this.stockRequestsList = [];
    }    
  }

}