import { Component, OnInit, TemplateRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NbDialogService } from '@nebular/theme';
import { AdminService } from '../../services/admin/admin.service';
import { AccessService } from '../../services/access/access.service';
import { alphaRegx, numRegx, nonHTMLRegx } from '../../helpers/regExp';
import { deleteConfirmAlert, successAlert, errorAlert } from '../../helpers/sweetalert';
import { getSumByKeyWithFilter, getSumByKeyWithFilters } from '../../helpers/common';
import { AuthenticateService } from '../../services/authentication/authenticate.service';

@Component({
  selector: 'ngx-used-stock',
  templateUrl: './used-stock.component.html',
  styleUrls: ['./used-stock.component.scss']
})
export class UsedStockComponent implements OnInit {

  searchForm!: FormGroup;
  addForm!: FormGroup;
  updateForm!: FormGroup;
  showModal! : boolean;
  submitted = false;
  userAccess = {view: true, edit: false, delete: false};
  page: number = 1;
  pageSize: number = 25;
  totalCount: number = 0;
  pageSizes = ['25', '50', '100', '200'];
  currentUserData: any;
  list: any;
  currentDate: any;
  pastDate: any;
  isSuperAdmin: boolean;
  branchID: any;
  branchesList: any;
  total3kgRequestsCount: any = 0;
  total5kgRequestsCount: any = 0;
  total10kgRequestsCount: any = 0;
  total15kgRequestsCount: any = 0;
  totalTapesRequestsCount: any = 0;
  totalCoversPacketRequestsCount: any = 0;
  totalBillBooksRequestsCount: any = 0;
  total3kgApprovedCount: any = 0;
  total5kgApprovedCount: any = 0;
  total10kgApprovedCount: any = 0;
  total15kgApprovedCount: any = 0;
  totalTapesApprovedCount: any = 0;
  totalCoversPacketApprovedCount: any = 0;
  totalBillBooksApprovedCount: any = 0;

  constructor(
    private formBuilder: FormBuilder,
    private adminService: AdminService,
    private accessService: AccessService,
    private dialogService: NbDialogService,
    private authenticateService: AuthenticateService,
  ) { }

  ngOnInit(): void {
    const accessData = this.accessService.getActionAccess('stock-requests');
    if(accessData && accessData.length > 0){
      this.userAccess.edit = accessData[0].edit;
      this.userAccess.delete = accessData[0].delete;
    }

    this.currentUserData = this.authenticateService.currentUserValue;
    if(this.currentUserData && this.currentUserData.role_type && this.currentUserData.role_type !== 'Super-Admin'){
      this.isSuperAdmin = false;
      this.branchID = this.currentUserData.branch_id;
    }else if(this.currentUserData && this.currentUserData.role_type && this.currentUserData.role_type === 'Super-Admin'){
      this.isSuperAdmin = true;
    }

    this.currentDate = new Date().toISOString().split('T')[0];
    let now = new Date();
    const backDate = new Date(now.setDate(now.getDate() - 30));
    this.pastDate = backDate.toISOString().split('T')[0];
    
    this.searchForm = this.formBuilder.group({
      from_date: [this.pastDate, Validators.required],
      to_date: [this.currentDate],
      branch: [],
    });

    this.addForm = this.formBuilder.group({
      branch_id: [this.branchID],
      stock_type: [, [Validators.required, Validators.pattern(alphaRegx)]],
      quantity: ['', [Validators.required, Validators.pattern(numRegx)]],
      description: ['', [Validators.pattern(nonHTMLRegx)]]
    });
    this.updateForm = this.formBuilder.group({
      id: ['', [Validators.required]],
      branch_id: ['', [Validators.required]],
      stock_type: ['', [Validators.required, Validators.pattern(alphaRegx)]],
      quantity: ['', [Validators.required, Validators.pattern(numRegx)]],
      description: ['', [Validators.pattern(nonHTMLRegx)]]
    });

    this.getBranchesList();
    this.getUsedStockList();
  }

  changePageSize = (event: any) => {    
    this.pageSize = event.target.value;
  }

  openModal(dialog: TemplateRef<any>) {
    this.dialogService.open(
      dialog,
      {
        context: '',
        closeOnBackdropClick: false,
      });
  }

  get form() { return this.addForm.controls; }
  get editForm() { return this.updateForm.controls; }

  edit = (data: any, modalVal: any) => {
    const updateData = {
      id: data.id,
      branch_id: data.branch_id,
      stock_type: data.stock_type,
      quantity: data.quantity,
      description: data.description
    }
    this.updateForm.setValue(updateData);
    this.openModal(modalVal);
  }

  handleStockTypeChange = (val: any, formVal: any = 'add') => {
    if(formVal === 'update' && val !== 'BOXES'){
      this.updateForm.controls['box_weight'].setValue('N/A');
    }else if(val !== 'BOXES'){
      this.addForm.controls['box_weight'].setValue('N/A');
    }
  }

  handleDateChange = (event: any) => {
    this.searchForm.controls['to_date'].setValue(null);
    let fromDate = this.searchForm.value.from_date;
    if(fromDate){
      let now = new Date(fromDate);
      const futureDate = new Date(now.setDate(now.getDate()));
      let date: any = futureDate.toISOString().split('T');
      document.getElementById('to_date').setAttribute('min', date[0]);
    }
  }

  handleBranchChange = (branchID: any) => {
    this.branchID = branchID ? branchID : null;
  }

  getBranchesList = async() => {
    try {
      let resp = await this.adminService.getData('getBranches');          
      if(resp && resp.responseCode === 1){
        this.branchesList = resp.branchesList;
      }
    } catch (error) {
      this.branchesList = {};
    }    
  }

  getUsedStockList = async() => {
    try {
      let branchID = this.searchForm.value.branch ? this.searchForm.value.branch : null;
      if(branchID){
        this.branchID = branchID;
        this.addForm.controls['branch_id'].setValue(branchID);
      }else{
        this.addForm.controls['branch_id'].setValue(null);
      }
      let reqData = {
        branch_id: branchID,
        from_date: this.searchForm.value.from_date ? this.searchForm.value.from_date : null,
        to_date: this.searchForm.value.to_date ? this.searchForm.value.to_date : null,
      };
      
      let resp = await this.adminService.getData('getUsedStockList', reqData);          
      if(resp && resp.responseCode === 1){
        this.list = resp.list;
        this.totalCount = (resp.list).length;
      }
    } catch (error) {
      this.list = '';
    }    
  }

  openActionDropDown = (i: any) => {       
    let dropdowns = document.getElementsByClassName("dropdown-menu");
    for (let i = 0; i < dropdowns.length; i++) {
      var openDropdown = dropdowns[i];
      if (openDropdown.classList.contains('show')) {
        openDropdown.classList.remove('show');
      }
    } 
    document.getElementById("actionDropDown-" + i).classList.toggle("show");
  }

  addFormSubmit = async() => {
    try {
      if(this.userAccess.edit){
        this.submitted = true;
        // stop here if form is invalid
        if (this.addForm.invalid) {
          this.submitted = false;
            return;
        }
        let reqData = {...this.addForm.value, branch_id: this.branchID};
        const resp = await this.adminService.addOrUpdateData('addUsedStock', reqData);
        if(resp && resp.responseCode === 1){
          this.getUsedStockList();
          await successAlert("Details added successfully")
        }else if(resp && resp.responseCode === 0){
          await errorAlert(resp.message);
        }else{
          await errorAlert("Details adding has failed ! Please try again");
        }
      }else{
        await errorAlert("Access Forbidden ! Please contact administrator");
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }    
    this.submitted = false;
    this.addForm.reset();
    document.getElementById("close_add_modal")?.click()
  }

  updateFormSubmit = async() => {
    try {
      if(this.userAccess.edit){
        this.submitted = true;
        // stop here if form is invalid
        if (this.updateForm.invalid) {
          this.submitted = false;
            return;
        }
        const updateResp = await this.adminService.addOrUpdateData('updateUsedStock', this.updateForm.value);
        if(updateResp && updateResp.responseCode === 1){
          this.getUsedStockList();
          await successAlert("Details updated successfully")
        }else if(updateResp && updateResp.responseCode === 0){
          await errorAlert(updateResp.message);
        }else{
          await errorAlert("Details updating has failed ! Please try again");
        }
      }else{
        await errorAlert("Access Forbidden ! Please contact administrator");
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }    
    this.submitted = false;
    document.getElementById("close_edit_modal")?.click()
  }

  delete = async(id: String) => {
    try {
      if(this.userAccess.delete){
        if(await deleteConfirmAlert('Want to delete this')){
          const deleteResp = await this.adminService.deleteData('deleteUsedStock', id);
          if(deleteResp && deleteResp.responseCode === 1){
            this.getUsedStockList();
            await successAlert("Details deleted successfully")
          }else{
            await errorAlert("Details deleting has failed ! Please try again")
          }
        }
      }else{
        await errorAlert("Access Forbidden ! Please contact administrator");
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }    
  }

}