import { Component, OnInit, TemplateRef, ViewChild, ElementRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NbDialogService, NbSidebarService } from '@nebular/theme';
import { AdminService } from '../../services/admin/admin.service';
import { AccessService } from '../../services/access/access.service';
import { alphaRegx, mobileRegx, addressRegx, nonHTMLRegx } from '../../helpers/regExp';
import { AuthenticateService } from '../../services/authentication/authenticate.service';
import { exportToExcel } from '../../helpers/common';
import { errorAlert, successAlert } from '../../helpers/sweetalert';

@Component({
  selector: 'ngx-shipments-list',
  templateUrl: './shipments-list.component.html',
  styleUrls: ['./shipments-list.component.scss']
})


export class ShipmentsListComponent implements OnInit {

  searchForm!: FormGroup;
  updateForm!: FormGroup;
  updateTrackingInfoForm!: FormGroup;
  showModal! : boolean;
  submitted = false;
  editBtnDisable = false;
  adminsList: any;
  roles: any;
  userAccess = {view: true, edit: false, delete: false};
  page: number = 1;
  pageSize: number = 25;
  totalCount: number = 0;
  search_string1: string = '';
  search_string2: string = '';
  search_string3: string = '';
  search_string4: string = '';
  pageSizes = ['25', '50', '100', '200'];
  currentUserData: any;
  branchesList: any;
  shipmentsList: any;
  isSuperAdmin: Boolean = true;
  branchID: any;
  currentDate: any;
  pastDate: any;

  @ViewChild('pdfHTML') pdfHTML!: ElementRef;

  constructor(
    private formBuilder: FormBuilder,
    private adminService: AdminService,
    private accessService: AccessService,
    private dialogService: NbDialogService,
    private authenticateService: AuthenticateService,
    private sidebarService: NbSidebarService,
  ) { }


  ngOnInit(): void {
    const accessData = this.accessService.getActionAccess('users');
    if(accessData && accessData.length > 0){
      this.userAccess.edit = accessData[0].edit;
      this.userAccess.delete = accessData[0].delete;
    }

    this.currentUserData = this.authenticateService.currentUserValue;
    if(this.currentUserData && this.currentUserData.role_type && this.currentUserData.role_type !== 'Super-Admin'){
      this.isSuperAdmin = false;
      this.branchID = this.currentUserData.branch_id ? this.currentUserData.branch_id : null;
    }

    this.currentDate = new Date().toISOString().split('T')[0];
    let now = new Date();
    const backDate = new Date(now.setDate(now.getDate() - 30));
    this.pastDate = backDate.toISOString().split('T')[0];
    
    this.searchForm = this.formBuilder.group({
      from_date: [this.pastDate, Validators.required],
      to_date: [this.currentDate],
      branch: [],
    });

    this.updateForm = this.formBuilder.group({
      admin_id: ['', [Validators.required]],
      role: ['', [Validators.required]],
      branch_id: ['', [Validators.required]],
      first_name: ['', [Validators.required, Validators.pattern(alphaRegx), Validators.minLength(3), Validators.maxLength(30)]],
      last_name: ['', [Validators.pattern(alphaRegx), Validators.maxLength(30)]],
      email: ['', [Validators.required, Validators.email]],
      mobile_number: ['', [Validators.required, Validators.pattern(mobileRegx)]],
      address: ['', [Validators.pattern(addressRegx), Validators.minLength(10), Validators.maxLength(250)]]
    });

    this.updateTrackingInfoForm = this.formBuilder.group({
      id: ['', [Validators.required]],
      tracking_no1_old: ['', [Validators.required]],
      tracking_no1: ['', [Validators.required, Validators.pattern(nonHTMLRegx)]],
      tracking_no2: ['', [Validators.pattern(nonHTMLRegx)]],
      tracking_no3: ['', [Validators.pattern(nonHTMLRegx)]],
      tracking_no4: ['', [Validators.pattern(nonHTMLRegx)]],
    });

    this.getShipmentsList();
    this.getBranchesList();
    this.toggleSidebar();
  }

  get form() { return this.updateForm.controls; }
  get trackingForm() { return this.updateTrackingInfoForm.controls; }

  editTrackingInfo = (data: any, modalVal: any) => {
    const updateData = {
      id: data.id,
      tracking_no1_old: data.tracking_no1,
      tracking_no1: data.tracking_no1,
      tracking_no2: data.tracking_no2,
      tracking_no3: data.tracking_no3,
      tracking_no4: data.tracking_no4
    }
    this.updateTrackingInfoForm.setValue(updateData);
    this.openModal(modalVal);
  }

  toggleSidebar(): boolean {
    this.sidebarService.toggle(true, 'menu-sidebar');
    return false;
  }

  changePageSize = (event: any) => {    
    this.pageSize = event.target.value;
  }

  openModal(dialog: TemplateRef<any>) {
    this.dialogService.open(
      dialog,
      {
        context: '',
        closeOnBackdropClick: false,
      });
  }

  handleDateChange = (event: any) => {
    this.searchForm.controls['to_date'].setValue(null);
    let fromDate = this.searchForm.value.from_date;
    if(fromDate){
      let now = new Date(fromDate);
      const futureDate = new Date(now.setDate(now.getDate()));
      let date: any = futureDate.toISOString().split('T');
      document.getElementById('to_date').setAttribute('min', date[0]);
    }
  }

  checkUpdateAvailable = (createdAt: any) => {
    let currentDate: any = new Date().toISOString().split('T')[0];
    let createdDate: Date = new Date(createdAt);
    let futureDate = new Date(createdDate.setDate(createdDate.getDate() + 2)).toISOString().split('T')[0];
    return futureDate >= currentDate || this.currentUserData.role_type === 'Super-Admin';
  }

  getShipmentsList = async() => {
    try {
      let reqData = {
        branch_id: this.searchForm.value.branch ? this.searchForm.value.branch : null,
        from_date: this.searchForm.value.from_date ? this.searchForm.value.from_date : null,
        to_date: this.searchForm.value.to_date ? this.searchForm.value.to_date : null,
      };
      let resp = await this.adminService.getData('getShipmentsList', reqData);          
      if(resp && resp.responseCode === 1){
        this.shipmentsList = resp.shipments;
        this.totalCount = (resp.shipments).length;
      }
    } catch (error) {
      this.shipmentsList = '';
    }    
  }

  getBranchesList = async() => {
    try {
      let resp = await this.adminService.getData('getBranches');          
      if(resp && resp.responseCode === 1){
        this.branchesList = resp.branchesList;
      }
    } catch (error) {
      this.branchesList = {};
    }    
  }

  exportToExcel = async() => {
    if(this.shipmentsList.length > 0){
      let sheetName = 'Shipment';
      let fileName = 'Shipment-' + new Date().getTime();
      let list = this.shipmentsList.map((data: any) => {
        let info = {
          'Branch': data.branch.branch_name,
          'Date': data.date,
          'Invoice No': data.invoice_number,
          'Service Type': data.carrier ? data.carrier.carrier_name : '',
          'Destination Country': data.country ? data.country.country_name : data.destination_country,
          'Tracking No 1': data.tracking_no1,
          'Tracking No 2': data.tracking_no2,
          'Tracking No 3': data.tracking_no3,
          'Tracking No 4': data.tracking_no4,
          'No Of Pieces': data.no_of_pieces,
          '3KG': data.boxes_3kg,
          '5KG': data.boxes_5kg,
          '10KG': data.boxes_10kg,
          '15KG': data.boxes_15kg,
          'Custom': data.boxes_custom,
          'Actual Weight': data.actual_weight,
          'Volumetric Weight': data.valumetric_weight,
          'Chargable Weight': data.chargable_weight,
          'Is Medicine Shipment': data.medicine_shipment,
          'Product Type': data.product_type,
          'Basic Charges': data.basic_amount,
          'GST(18%)': data.gst_amount,
          'Others': data.other_amount,
          'Total Amount': data.total_amount,
          'Mode Of Payment': data.mode_of_payment,
          'Transaction ID': data.transaction_id,
          'Sender Name': data.sender_name,          
          'Sender Company Name': data.sender_company_name,
          'Sender Address Line1': data.sender_address1,
          'Sender Address Line2': data.sender_address2,
          'Sender Address Line3': data.sender_address3,
          'Sender Postal Code': data.sender_pincode,
          'Sender City': data.sender_city,
          'Sender State': data.sender_state,
          'Sender Phone Number': data.sender_phone_number,
          'Sender Email ID': data.sender_email,
          'Sender ID Type': data.sender_id_proof_type,  
          'Sender ID Number': data.sender_id_proof_number,  
          'Receiver Name': data.receiver_name,  
          'Receiver Company Name': data.receiver_company_name,  
          'Receiver Address Line1': data.receiver_address1,  
          'Receiver Address Line2': data.receiver_address2,  
          'Receiver Address Line3': data.receiver_address3,  
          'Receiver Postal Code': data.receiver_pincode,  
          'Receiver City': data.receiver_city,  
          'Receiver State': data.receiver_state,  
          'Receiver Phone Number': data.receiver_phone_number,  
          'Receiver Email ID': data.receiver_email,  
          'Commodity Summery Of The Items': data.commodity_items_summery,  
          'Remarks': data.remarks,
          'Created On': new Date(data.createdAt).toLocaleString(),
          'Last Updated On': new Date(data.updatedAt).toLocaleString(),
        };
        return info;
      });
      await exportToExcel(list, fileName, sheetName);
    }
  }

  downloadKycFiles = (files: any) => {
    if(files){
      let fileNames = files.split(',');
      fileNames.map((fileName: any) => {
        this.adminService.downLoadFiles('kyc-files', fileName);
      });      
    }    
  }

  downloadInvoicePDF = (data: any) => {
    this.adminService.downLoadInvoicePDF(data);    
  }

  openActionDropDown = (i: any) => {        
    document.getElementById("actionDropDown-" + i).classList.toggle("show");
  }

  searchFormSubmit = async() =>{
    if(this.isSuperAdmin){
      this.getShipmentsList();
    }
  }

  updateTrackingInfoFormSubmit = async() => {
    try {
      if(this.userAccess.edit){
        this.submitted = true;
        // stop here if form is invalid
        if (this.updateTrackingInfoForm.invalid) {
          this.submitted = false;
            return;
        }
        const updateResp = await this.adminService.addOrUpdateData('updateShipmentTrackingNos', this.updateTrackingInfoForm.value);
        if(updateResp && updateResp.responseCode === 1){
          this.getShipmentsList();
          await successAlert("Details updated successfully")
        }else if(updateResp && updateResp.responseCode === 0){
          await errorAlert(updateResp.message);
        }else{
          await errorAlert("Details updating has failed ! Please try again");
        }
      }else{
        await errorAlert("Access Forbidden ! Please contact administrator");
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }    
    this.submitted = false;
    document.getElementById("close_edit_modal")?.click()
  }

}