import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';

import { PagesComponent } from './pages.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { ModulesListComponent } from './modules-list/modules-list.component';
import { RolesComponent } from './roles/roles.component';
import { BranchesComponent } from './branches/branches.component';
import { UsersComponent } from './users/users.component';
import { CountriesComponent } from './countries/countries.component';
import { CarriersComponent } from './carriers/carriers.component';
import { CarrierZonesComponent } from './carrier-zones/carrier-zones.component';
import { PricesComponent } from './prices/prices.component';
import { CommodityListComponent } from './commodity-list/commodity-list.component';
import { AddShipmentsComponent } from './add-shipments/add-shipments.component';
import { EmptyTrackingShipmentsComponent } from './empty-tracking-shipments/empty-tracking-shipments.component';
import { ShipmentsListComponent } from './shipments-list/shipments-list.component';
import { UpdateShipmentsComponent } from './update-shipments/update-shipments.component';
import { ViewShipmentDetailsComponent } from './view-shipment-details/view-shipment-details.component';
import { SettingsComponent } from './settings/settings.component';
import { NotFoundComponent } from './not-found/not-found.component';
import { AccessDeniedComponent } from './access-denied/access-denied.component';
import { BankTransactionsComponent } from './bank-transactions/bank-transactions.component';
import { BranchPaymentDetailsComponent } from './branch-payment-details/branch-payment-details.component';
import { ManageBranchCommissionsComponent } from './manage-branch-commissions/manage-branch-commissions.component';
import { BranchCommissionPaymentsComponent } from './branch-commission-payments/branch-commission-payments.component';
import { ExpensesComponent } from './expenses/expenses.component';
import { StockPurchasesComponent } from './stock-purchases/stock-purchases.component';
import { StockRequestsComponent } from './stock-requests/stock-requests.component';
import { StockAvailabilityComponent } from './stock-availability/stock-availability.component';
import { BranchStockAvailabilityComponent } from './branch-stock-availability/branch-stock-availability.component';
import { UsedStockComponent } from './used-stock/used-stock.component';
import { SalariesComponent } from './salaries/salaries.component';
import { ComplaintsComponent } from './complaints/complaints.component';

import { AuthGuard } from '../helpers/auth.guard';

const routes: Routes = [{
  path: '',
  component: PagesComponent,
  canActivate: [AuthGuard],
  children: [
    {
      path: '',
      redirectTo: 'dashboard',
      pathMatch: 'full',
    },
    {
      path: 'dashboard',
      component: DashboardComponent,
    },
    {
      path: 'modules-list',
      component: ModulesListComponent,
    },
    {
      path: 'roles',
      component: RolesComponent,
    },
    {
      path: 'branches',
      component: BranchesComponent,
    },
    {
      path: 'manage-branch-commissions/:id',
      component: ManageBranchCommissionsComponent,
    },
    {
      path: 'users',
      component: UsersComponent,
    },
    {
      path: 'countries',
      component: CountriesComponent,
    },
    {
      path: 'carriers',
      component: CarriersComponent,
    },
    {
      path: 'carrier-zones',
      component: CarrierZonesComponent,
    },
    {
      path: 'prices',
      component: PricesComponent,
    },
    {
      path: 'commodity-list',
      component: CommodityListComponent,
    },
    {
      path: 'add-shipments',
      component: AddShipmentsComponent,
    },
    {
      path: 'empty-tracking-shipments',
      component: EmptyTrackingShipmentsComponent,
    },
    {
      path: 'shipments-list',
      component: ShipmentsListComponent,
    },
    {
      path: 'update-shipments/:id',
      component: UpdateShipmentsComponent,
    },
    {
      path: 'view-shipment-details/:id',
      component: ViewShipmentDetailsComponent,
    },
    {
      path: 'bank-transactions',
      component: BankTransactionsComponent,
    },
    {
      path: 'branch-payment-details',
      component: BranchPaymentDetailsComponent,
    },    
    {
      path: 'branch-commission-payments',
      component: BranchCommissionPaymentsComponent,
    },
    {
      path: 'expenses',
      component: ExpensesComponent,
    },
    {
      path: 'stock-purchases',
      component: StockPurchasesComponent,
    },
    {
      path: 'stock-requests',
      component: StockRequestsComponent,
    },
    {
      path: 'stock-availability',
      component: StockAvailabilityComponent,
    },
    {
      path: 'branch-stock-availability',
      component: BranchStockAvailabilityComponent,
    },
    {
      path: 'used-stock',
      component: UsedStockComponent,
    },
    {
      path: 'salaries',
      component: SalariesComponent,
    },
    {
      path: 'complaints',
      component: ComplaintsComponent,
    },
    {
      path: 'settings',
      component: SettingsComponent,
    },
    {
      path: 'access-denied',
      component: AccessDeniedComponent,
    },
    {
      path: '**',
      component: NotFoundComponent,
    },
  ],
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PagesRoutingModule {
}
