import { Component, OnInit, TemplateRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NbDialogService } from '@nebular/theme';
import { AdminService } from '../../services/admin/admin.service';
import { AccessService } from '../../services/access/access.service';
import { alphaRegx, numRegx, nonHTMLRegx } from '../../helpers/regExp';
import { deleteConfirmAlert, successAlert, errorAlert } from '../../helpers/sweetalert';
import { getSumByKeyWithFilter } from '../../helpers/common';

@Component({
  selector: 'ngx-stock-purchases',
  templateUrl: './stock-purchases.component.html',
  styleUrls: ['./stock-purchases.component.scss']
})
export class StockPurchasesComponent implements OnInit {

  searchForm!: FormGroup;
  addForm!: FormGroup;
  updateForm!: FormGroup;
  showModal! : boolean;
  submitted = false;
  userAccess = {view: true, edit: false, delete: false};
  page: number = 1;
  pageSize: number = 25;
  totalCount: number = 0;
  pageSizes = ['25', '50', '100', '200'];
  currentUserData: any;
  list: any;
  totalList: any;
  currentDate: any;
  pastDate: any;
  total3kgCount: any = 0;
  total5kgCount: any = 0;
  total10kgCount: any = 0;
  total15kgCount: any = 0;
  totalTapesCount: any = 0;
  totalCoversPacketCount: any = 0;
  totalBillBooksCount: any = 0;
  total3kgFilteredCount: any = 0;
  total5kgFilteredCount: any = 0;
  total10kgFilteredCount: any = 0;
  total15kgFilteredCount: any = 0;
  totalTapesFilteredCount: any = 0;
  totalCoversPacketFilteredCount: any = 0;
  totalBillBooksFilteredCount: any = 0;
  fromDate: any;
  toDate: any;

  constructor(
    private formBuilder: FormBuilder,
    private adminService: AdminService,
    private accessService: AccessService,
    private dialogService: NbDialogService,
  ) { }


  ngOnInit(): void {
    const accessData = this.accessService.getActionAccess('stock-purchases');
    if(accessData && accessData.length > 0){
      this.userAccess.edit = accessData[0].edit;
      this.userAccess.delete = accessData[0].delete;
    }

    this.currentDate = new Date().toISOString().split('T')[0];
    let now = new Date();
    const backDate = new Date(now.setDate(now.getDate() - 30));
    this.pastDate = backDate.toISOString().split('T')[0];
    
    this.searchForm = this.formBuilder.group({
      from_date: [this.pastDate, Validators.required],
      to_date: [this.currentDate]
    });

    this.addForm = this.formBuilder.group({
      purchased_date: ['', [Validators.required]],
      stock_type: [, [Validators.required, Validators.pattern(alphaRegx)]],
      box_weight: ['N/A', [Validators.required]],
      quantity: ['', [Validators.required, Validators.pattern(numRegx)]],
      description: ['', [Validators.pattern(nonHTMLRegx)]]
    });
    this.updateForm = this.formBuilder.group({
      id: ['', [Validators.required]],
      purchased_date: ['', [Validators.required]],
      stock_type: ['', [Validators.required, Validators.pattern(alphaRegx)]],
      box_weight: ['', [Validators.required]],
      quantity: ['', [Validators.required, Validators.pattern(numRegx)]],
      description: ['', [Validators.pattern(nonHTMLRegx)]]
    });

    this.getStockPurchases();
  }

  changePageSize = (event: any) => {    
    this.pageSize = event.target.value;
  }

  openModal(dialog: TemplateRef<any>) {
    this.dialogService.open(
      dialog,
      {
        context: '',
        closeOnBackdropClick: false,
      });
  }

  get form() { return this.addForm.controls; }
  get editForm() { return this.updateForm.controls; }

  edit = (data: any, modalVal: any) => {
    const updateData = {
      id: data.id,
      purchased_date: data.purchased_date,
      stock_type: data.stock_type,
      box_weight: data.box_weight,
      quantity: data.quantity,
      description: data.description
    }
    this.updateForm.setValue(updateData);
    this.openModal(modalVal);
  }

  handleStockTypeChange = (val: any, formVal: any = 'add') => {
    if(formVal === 'update' && val !== 'BOXES'){
      this.updateForm.controls['box_weight'].setValue('N/A');
    }else if(val !== 'BOXES'){
      this.addForm.controls['box_weight'].setValue('N/A');
    }
  }

  handleDateChange = (event: any) => {
    this.searchForm.controls['to_date'].setValue(null);
    let fromDate = this.searchForm.value.from_date;
    if(fromDate){
      let now = new Date(fromDate);
      const futureDate = new Date(now.setDate(now.getDate()));
      let date: any = futureDate.toISOString().split('T');
      document.getElementById('to_date').setAttribute('min', date[0]);
    }
  }

  datetotimestamp = (date: any) => {
    let time = new Date(date).getTime();
    return time;
  }

  getStockPurchases = async() => {
    try {
      let now = new Date();
      let backDate = new Date(now.setDate(now.getDate() - 30)).toISOString().split('T');
      this.fromDate = this.searchForm.value.from_date ? this.searchForm.value.from_date : backDate;
      this.toDate = this.searchForm.value.to_date ? this.searchForm.value.to_date : new Date().toISOString().split('T')[0];
      
      let resp = await this.adminService.getData('getStockPurchases');          
      if(resp && resp.responseCode === 1){
        this.totalList = resp.list;
        let filteredList = this.totalList.filter((data: any) => this.datetotimestamp(this.fromDate) <= this.datetotimestamp(data.purchased_date) && this.datetotimestamp(this.toDate) >= this.datetotimestamp(data.purchased_date));
        
        this.list = filteredList;
        this.totalCount = filteredList.length;
        
        this.total3kgCount = await getSumByKeyWithFilter(this.totalList, 'quantity', 'box_weight', '3KGS');
        this.total5kgCount = await getSumByKeyWithFilter(this.totalList, 'quantity', 'box_weight', '5KGS');
        this.total10kgCount = await getSumByKeyWithFilter(this.totalList, 'quantity', 'box_weight', '10KGS');
        this.total15kgCount = await getSumByKeyWithFilter(this.totalList, 'quantity', 'box_weight', '15KGS');
        this.totalTapesCount = await getSumByKeyWithFilter(this.totalList, 'quantity', 'stock_type', 'TAPES');
        this.totalCoversPacketCount = await getSumByKeyWithFilter(this.totalList, 'quantity', 'stock_type', 'COVERS PACKET');
        this.totalBillBooksCount = await getSumByKeyWithFilter(this.totalList, 'quantity', 'stock_type', 'BILL BOOKS');
        
        this.total3kgFilteredCount = await getSumByKeyWithFilter(this.list, 'quantity', 'box_weight', '3KGS');
        this.total5kgFilteredCount = await getSumByKeyWithFilter(this.list, 'quantity', 'box_weight', '5KGS');
        this.total10kgFilteredCount = await getSumByKeyWithFilter(this.list, 'quantity', 'box_weight', '10KGS');
        this.total15kgFilteredCount = await getSumByKeyWithFilter(this.list, 'quantity', 'box_weight', '15KGS');
        this.totalTapesFilteredCount = await getSumByKeyWithFilter(this.list, 'quantity', 'stock_type', 'TAPES');
        this.totalCoversPacketFilteredCount = await getSumByKeyWithFilter(this.list, 'quantity', 'stock_type', 'COVERS PACKET');
        this.totalBillBooksFilteredCount = await getSumByKeyWithFilter(this.list, 'quantity', 'stock_type', 'BILL BOOKS');
      }
    } catch (error) {
      this.list = '';
    }    
  }

  openActionDropDown = (i: any) => {       
    let dropdowns = document.getElementsByClassName("dropdown-menu");
    for (let i = 0; i < dropdowns.length; i++) {
      var openDropdown = dropdowns[i];
      if (openDropdown.classList.contains('show')) {
        openDropdown.classList.remove('show');
      }
    } 
    document.getElementById("actionDropDown-" + i).classList.toggle("show");
  }

  addFormSubmit = async() => {
    try {
      if(this.userAccess.edit){
        this.submitted = true;
        // stop here if form is invalid
        if (this.addForm.invalid) {
          this.submitted = false;
            return;
        }
        const resp = await this.adminService.addOrUpdateData('addStockPurchases', this.addForm.value);
        if(resp && resp.responseCode === 1){
          this.getStockPurchases();
          await successAlert("Details added successfully")
        }else if(resp && resp.responseCode === 0){
          await errorAlert(resp.message);
        }else{
          await errorAlert("Details adding has failed ! Please try again");
        }
      }else{
        await errorAlert("Access Forbidden ! Please contact administrator");
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }    
    this.submitted = false;
    this.addForm.reset();
    document.getElementById("close_add_modal")?.click()
  }

  updateFormSubmit = async() => {
    try {
      if(this.userAccess.edit){
        this.submitted = true;
        // stop here if form is invalid
        if (this.updateForm.invalid) {
          this.submitted = false;
            return;
        }
        const updateResp = await this.adminService.addOrUpdateData('updateStockPurchases', this.updateForm.value);
        if(updateResp && updateResp.responseCode === 1){
          this.getStockPurchases();
          await successAlert("Details updated successfully")
        }else if(updateResp && updateResp.responseCode === 0){
          await errorAlert(updateResp.message);
        }else{
          await errorAlert("Details updating has failed ! Please try again");
        }
      }else{
        await errorAlert("Access Forbidden ! Please contact administrator");
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }    
    this.submitted = false;
    document.getElementById("close_edit_modal")?.click()
  }

  delete = async(id: String) => {
    try {
      if(this.userAccess.delete){
        if(await deleteConfirmAlert('Want to delete this')){
          const deleteResp = await this.adminService.deleteData('deleteStockPurchases', id);
          if(deleteResp && deleteResp.responseCode === 1){
            this.getStockPurchases();
            await successAlert("Details deleted successfully")
          }else{
            await errorAlert("Details deleting has failed ! Please try again")
          }
        }
      }else{
        await errorAlert("Access Forbidden ! Please contact administrator");
      }
    } catch (error) {
      await errorAlert(error.message ? error.message : 'Something went wrong ! Please try again');
    }    
  }

}